.columns-layout {
    @extend .contacts-layout;

    &__left {
        @extend .contacts-layout__map;
        flex: 0 0 rem(790);
        overflow: hidden;
    }

    &__left-wrp {
        // position: absolute;
        margin-top: rem(140);
        @extend %padding-left;
        left: 0;
    }

    &__content {
        @extend .contacts-layout__content;
    }

    &__content-wrp {
        @extend .contacts-layout__content-wrp;
        top: rem(140);
    }

    &__content-scroll {
        @extend .contacts-layout__content-scroll;
        width: rem(767);
        height: rem(700);
    }

    &__label {
        @extend .contacts-layout__label;
    }
}

@media screen and (max-width: $laptop) {
    .columns-layout {
        &__left {
            flex: 0 0 rem(450);
        }

        &__content-scroll {
            width: rem(700);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .columns-layout {
        &__left {
            flex: 0 0 100%;
            height: auto;
            overflow: visible;
        }

        &__left-wrp {
            padding-right: 60px;
        }

        &__content-wrp {
            top: 0;
            position: relative;
            padding-right: 0;
            width: 100%;
        }

        &__content-scroll {
            width: 100%;
            height: auto;
        }
    }
}

@media screen and (max-width: $mobile) {
    .columns-layout {

        &__left-wrp {
            margin-top: 100px;
        }

        &__left-wrp {
            padding-right: 20px;
        }
    }
}