.slider-inner {
    height: rem(732);

    &__container {
        height: 100%;
    }

    &__slide {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
    }

    &__slide-wrp {
        overflow: hidden;
    }

    &__controls {
        display: flex;
        justify-content: flex-end;
    }

    &__controls-container {
        position: relative;
        padding: 0 rem(55);
        margin-top: rem(21);
    }

    &__counter-current {
        font-family: $font-secondary;
        font-size: rem(36);
        line-height: rem(43);
        color: $dark;
    }

    &__counter-count {
        font-family: $font-secondary;
        color: $dark;
    }

    &__counter-separator {
        font-family: $font-secondary;
        color: $dark;
    }

    &__arrow {
        position: absolute;
        top: 50%;
        outline: none;
        cursor: pointer;
        transform: translate3d(0, -50%, 0);

        &_left {
            left: 0;
        }

        &_right {
            right: 0;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slider-inner {
        height: rem(549);

        &__controls-container {
            padding: 0 rem(35);
            margin-top: rem(18);
        }
    
        &__counter-current {
            font-size: 28px;
            line-height: 34px;
        }
    
        &__arrow {
            svg {
                width: 19px;
                height: 9px;
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slider-inner {
        height: rem(273);

        &__controls-container {
            padding: 0 35px;
            margin-top: rem(10);
        }
    }
}

@media screen and (max-width: $mobile) {
    .slider-inner {
        margin-bottom: 70px;
        height: rem(148);
    
        &__counter-current {
            font-size: 18px;
            line-height: 22px;
        }

        &__arrow {
            top: 43%;
        }
    }
}
