.date-input {
    position: relative;
    margin-bottom: rem(110);
    overflow: hidden;
    transition: transform 2s cubic-bezier(.8,0,.14,1);

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__control {
        font-family: $font-secondary;
        letter-spacing: 0.1em;
        width: rem(100);
        height: rem(100);
        margin-right: rem(20);
        border: none;
        text-align: center;
        text-transform: uppercase;
        outline: 0;
        background: $green;
        font-size: rem(36);
        color: $green-light;

        &:last-of-type {
            margin-right: 0;
        }

        &_long {
            width: rem(200);
        }
    }

    &__error {
        position: absolute;
        bottom: rem(-80);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        user-select: none;
        font-size: rem(18);
        opacity: 0;
        transition: opacity .6s ease;
    }

    &__container {
        transform: translate3d(0, 100%, 0);
        transition: transform 2s cubic-bezier(.8,0,.14,1);
    }

    .error-icon {
        margin-right: rem(20);
    }
}

@media screen and (max-width: $laptop) {
    .date-input {
        margin-bottom: rem(85);

        &__control {
            height: rem(86);
            font-size: 28px;
            line-height: 34px;

            &_long {
                width: rem(172);
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .date-input {
        margin-bottom: rem(95);
    }
}

@media screen and (max-width: $mobile) {
    .date-input {
        margin-bottom: rem(55);

        &__control {
            height: rem(46);
            font-size: 18px;
            line-height: 24px;

            &_long {
                width: rem(87);
            }
        }
    }
}
