.slider-main {
    width: 100%;
    height: 100%;

    &__container {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    &__backgrounds, &__slides {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        transform-origin: 50% 50%;
    }

    &__backgrounds {
        left: 50%;
        top: 50%;
        transform: translate3d(-50%, -50%, 0);
    }

    &__background {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &_active {
            z-index: 1;
        }
    }

    &__background-media {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;

        video {
            @extend %video-cover;
        }
    }

    &__slide {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
        transition: opacity .1s .9s ease;

        &_active {
            z-index: 1;
            pointer-events: all;
        }
    }

    &__slide-desc {
        position: absolute;
        right: 50%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &__counter {
        position: absolute;
        bottom: rem(65);
        width: 100%;
        z-index: 1;
        display: flex;
        justify-content: center;
    }

    &__counter-value {
        display: inline-block;

        &_remove {
            position: absolute;
        }
    }

    &__counter-content {
        
    }

    &__counter-item {
        font-family: $font-secondary;
        font-size: 18px;

        &_current {
            font-size: rem(36);
            line-height: rem(36);
        }

        &_count, &_separator {
            
        }
    }

    .slide-title {
        &:hover {
            .slide-title {
                &__icon-inner {
                    transform: scale(1.1);

                    svg {
                        // animation: swing-right 2s ease infinite;
                    }
                }

                &__icon-wrp {
                    transform: scale(0.9);
                }
            }
        }
    }

    .swiper-container {
        width: inherit;
        height: inherit;
    }
}

@keyframes swing-right {
    from { transform: none }
    33% { transform: translate3d(25%, 0, 0) }
    to { transform: none }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .slider-main {
        &__background-media {
            video {
                width: 100%;
                height: auto;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate3d(-50%, -50%, 0);
            }
        }

        &__backgrounds {
            left: 0;
            top: 0;
            transform: none;
        }
    }
}

@supports (-ms-ime-align: auto) {
    .slider-main {
        &__background-media {
            video {
                width: 100%;
                height: auto;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate3d(-50%, -50%, 0);
            }
        }

        &__backgrounds {
            left: 0;
            top: 0;
            transform: none;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slider-main {
        &__counter {
            bottom: 36px;
        }

        &__counter-item {

            &_current {
                font-size: 28px;
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slider-main {
        &__slide-desc {
            right: 0;
            left: 0;
            justify-content: center;
        }
    }
}

@media screen and (max-width: $mobile) {
    .slider-main {
        height: var(--app-height);

        &__counter {
            display: none;
        }
    }
}
