.menu-dropdown {
    position: relative;

    &__btn {
        position: relative;
        padding-right: 28px;
        cursor: pointer;
    }

    &__text {
        @extend .menu-mobile__link;
    }

    &__icon {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate3d(0, -50%, 0);

        svg {
            transition: transform .5s ease;
        }

        path {
            stroke: white;
        }
    }

    &__container {
        overflow: hidden;
        height: 0;
        transition: height .3s ease;
    }

    &__item {
        font-family: $font-secondary;
        margin-top: 25px;
        font-size: 22px;
        line-height: 26px;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: uppercase;

        &:first-child {
            margin-top: 30px;
        }
    }

    &_active {
        .menu-dropdown {
            &__icon {

                svg {
                    transform: rotate(-180deg);
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .menu-dropdown {
        &__item {
            font-size: 20px;
            line-height: 24px;
            margin-top: 17px;

            &:first-child {
                margin-top: 25px;
            }
        }

        &__container {
            padding: 0 20px;
        }

        &__btn {
            display: inline-block;
            padding-right: 0;
        }

        &__icon {
            margin-right: -28px;
        }
    }
}
