.text-icons {
    &__container {
        display: flex;
        flex-wrap: wrap;
    }

    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid #E1D9C6;
        background: $skinny;
        border-radius: 50%;
        @extend %circle;
        margin-right: rem(53);
        margin-bottom: rem(53);

        &:last-child {
            margin-right: 0;
        }
    }
}

@media screen and (max-width: $laptop) {
    .text-icons {
        &__icon {
            width: 65px;
            height: 65px;
            margin-right: rem(40);
            margin-bottom: rem(40);
        }
    }
}

@media screen and (max-width: $mobile) {
    .text-icons {
        &__icon {
            width: 40px;
            height: 40px;
            margin-right: rem(20);
            margin-bottom: rem(20);
        }
    }
}
