.form-limiter {
    margin-top: rem(160);
    max-width: 800px;
    @extend %col-offset;
}

@media screen and (max-width: $laptop) {
    .form-limiter {
        max-width: 606px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .form-limiter {
        margin-top: rem(107);
        max-width: 100%;
    }
}

@media screen and (max-width: $mobile) {
    .form-limiter {
        margin-top: 77px;
    }
}
