.submit-message {
    position: fixed;
    top: 50%;
    left: 50%;
    animation: fadeIn .6s ease forwards;
    z-index: 9999;
    color: $dark;

    &__inner {
        padding: rem(80) rem(30) rem(40);
        background: $skinny;
        box-shadow: 0 rem(2) rem(8) rgba(0, 0, 0, .1);
        width: rem(400);
    }

    &__icon {
        display: flex;
        align-items: center;
        margin-right: rem(20);

        > img {
            width: rem(10);
            height: rem(32);
            transition: transform .3s ease;
        }
    }

    &__close {
        display: flex;
        align-items: center;
        position: absolute;
        top: rem(30);
        right: rem(30);
        cursor: pointer;

        &:hover {
            .submit-message {
                &__icon {
                    > img {
                        &:nth-child(1) {
                            transform: translateX(rem(10));
                        }

                        &:nth-child(2) {
                            transform: translateX(rem(-10));
                        }
                    }
                }
            }
        }
    }

    &__title {
        font-family: $font-secondary;
        font-size: rem(24);
        margin-bottom: rem(20);

        .text-stamp {
            -webkit-text-stroke: rem(1) #fff;
        }
    }

    &__text {
        line-height: 1.4;
    }

    &_animate {
        animation: fadeOut .6s ease forwards;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate3d(-50%, -40%, 0);
    }

    to {
        opacity: 1;
        transform: translate3d(-50%, -50%, 0);
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: translate3d(-50%, -50%, 0);
    }

    to {
        opacity: 0;
        transform: translate3d(-50%, -40%, 0);
    }
}

@media screen and (max-width: $laptop) {
    .submit-message {
        &__inner {
            padding: rem(80, 16) rem(30, 16) rem(40, 16);
            width: rem(400, 16);
        }

        &__icon {
            margin-right: rem(20, 16);

            > img {
                width: rem(10, 16);
                height: rem(32, 16);
            }
        }

        &__close {
            font-size: rem(18, 16);
            top: rem(30, 16);
            right: rem(30, 16);

            &:hover {
                .submit-message {
                    &__icon {
                        > img {
                            &:nth-child(1) {
                                transform: translateX(rem(10, 16));
                            }

                            &:nth-child(2) {
                                transform: translateX(rem(-10, 16));
                            }
                        }
                    }
                }
            }
        }

        &__title {
            font-size: rem(24, 16);
        }

        &__text {
            font-size: rem(18, 16);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .submit-message {
        &__inner {
            width: rem(320, 16);
        }

        &__icon {
            margin-right: rem(10, 16);

            > img {
                width: rem(7.5, 16);
                height: rem(24, 16);
            }
        }

        &__close {
            font-size: rem(16, 16);

            &:hover {
                .submit-message {
                    &__icon {
                        > img {
                            &:nth-child(1) {
                                transform: translateX(rem(7.5, 16));
                            }

                            &:nth-child(2) {
                                transform: translateX(rem(-7.5, 16));
                            }
                        }
                    }
                }
            }
        }

        &__title {
            font-size: rem(18, 16);
        }

        &__text {
            font-size: rem(16, 16);
        }
    }
}

@media screen and (max-width: $mobile) {
    .submit-message {
        width: 90%;

        &__inner {
            padding: rem(80, 16) rem(15, 14) rem(40, 14);
            width: 100%;
        }

        &__icon {
            margin-right: rem(10, 14);

            > img {
                width: rem(7.5, 14);
                height: rem(24, 14);
            }
        }

        &__close {
            font-size: rem(16, 14);

            &:hover {
                .submit-message {
                    &__icon {
                        > img {
                            &:nth-child(1) {
                                transform: translateX(rem(7.5, 14));
                            }

                            &:nth-child(2) {
                                transform: translateX(rem(-7.5, 14));
                            }
                        }
                    }
                }
            }
        }

        &__title {
            font-size: rem(18, 14);
        }

        &__text {
            font-size: rem(16, 14);
        }
    }
}
