.menu {
    @extend %list-normalize;
    display: flex;

    &__item {
        position: relative;
        margin-right: rem(60);
        transition: color .5s ease;

        &:last-child {
            margin-right: 0;
        }

        &::before {
            @extend %pseudo;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background: $main-theme;
            top: -12px;
            left: 2px;
            opacity: 0;
            transition: opacity .5s ease;
            animation: blob-up .1s linear forwards;
        }

        &_active, &:hover {
            &::before {
                opacity: 1;
                transition: opacity 0s ease;
                animation: blob-down .5s linear forwards;
            }
        }
    }

    &__link {
        @extend %link-normalize;
        // color: $ui-color;
    }

    &_dark {
        .menu {
            &__item {
                color: $dark;
            }
        }

        .dropdown {
            &__icon {
                path {
                    stroke: $dark;
                }
            }
        }
    }

    &_hidden {
        display: none;
    }
}

@keyframes blob-up {
    0% {
        transform: translate3d(0, 0, 0);
    }

    100% {
        transform: translate3d(0, -7px, 0);
    }
}

@keyframes blob-down {
    0% {
        transform: translate3d(0, -28px, 0) scaleY(1.3);
    }

    5% {
        transform: translate3d(0, -21px, 0) scaleY(1.8);
    }

    10% {
        transform: translate3d(0, -14px, 0) scaleY(2);
    }

    20% {
        transform: translate3d(0, -7px, 0) scaleY(1.5);
    }

    30% {
        transform: translate3d(0, 0, 0) scaleY(1) scaleX(1.8);
    }

    45% {
        transform: translate3d(0, -5px, 0) scaleY(1.2) scaleX(1);
    }

    60% {
        transform: translate3d(0, 0, 0) scaleY(1) scaleX(1.2);
    }

    75% {
        transform: translate3d(0, -1px, 0) scaleY(1.1) scaleX(1);
    }

    90% {
        transform: translate3d(0, 0, 0);
    }

    100% {
        transform: translate3d(0, 0, 0);
    }
}

@media screen and (max-width: $laptop) {
    .menu {
        &__item {
            margin-right: rem(53);
        }
    }
}
