.block-contact {
    margin: rem(100) 0 0;
    display: flex;
    justify-content: center;

    &__container {
        text-align: center;
        max-width: 800px;
    }

    &__title {
        font-family: $font-secondary;
        font-size: 36px;
        line-height: 43px;
        margin-bottom: 30px;
        color: $dark;
    }

    &__item {
        font-size: 14px;
        line-height: 28px;
        color: $dark;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__line {
        position: relative;
        padding-left: 32px;
        display: inline-block;
    }

    &__icon {
        position: absolute;
        left: 0;
        top: 13px;
        transform: translate3d(0, -50%, 0);
    }

    &__text {
        a {
            display: inline-block;
            margin-right: 10px;
        }
    }
}

@media screen and (max-width: $laptop) {
    .block-contact {
        &__title {
            font-size: 28px;
            line-height: 34px;
            margin-bottom: 25px;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .block-contact {
        margin: 65px 0 0 0;
    }
}

@media screen and (max-width: $mobile) {
    .block-contact {
        margin: 40px 0 0 0;

        &__item {
            margin-bottom: 15px;
            line-height: 20px;
        }

        &__title {
            font-size: 18px;
            line-height: 22px;
            margin-bottom: 25px;
        }

        &__line {
            padding-left: 0;
        }

        &__icon {
            display: none;
        }

        &__text {
            a {
                display: block;
            }
        }
    }
}
