.lang {
    position: fixed;
    z-index: 997;
    top: rem(71);
    @extend %absolute-right;

    &__list {
        @extend %list-normalize;
        display: flex;
    }

    &__item {
        transition: color .5s ease;
        color: $ui-color;
    }

    &__link {
        @extend %link-normalize;
    }

    &_dark {
        .lang {
            &__item {
                color: $dark;
                color: $ui-color;
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .lang {
        top: 43px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .lang {
        top: 47px;
        // position: absolute;
    }
}

@media screen and (max-width: $mobile) {
    .lang {
        top: 30px;
        right: 80px;
    }
}
