.slider-blocks {
    height: rem(590);
    margin: rem(70) 0 rem(120);

    &__container {
        height: 100%;
    }

    &__slide {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
        position: relative;
        cursor: pointer;
    }

    &__text {
        position: absolute;
        bottom: rem(30);
        left: rem(30);
        padding-right: rem(90);
    }

    &__pos {
        font-size: 16px;
        line-height: 26px;
        color: #fff;
        margin-top: 10px;
    }

    &__name {
        font-family: $font-secondary;
        font-size: rem(36);
        line-height: rem(43);
    }

    &__slide-wrp {
        overflow: hidden;
    }

    &__controls {
        @extend .slider-inner__controls;
        min-height: 50px;
    }

    &__controls-container {
        @extend .slider-inner__controls-container;
    }

    &__counter {
        display: none;
    }

    &__counter-current {
        @extend .slider-inner__counter-current;
    }

    &__counter-count {
        @extend .slider-inner__counter-count;
    }

    &__counter-separator {
        @extend .slider-inner__counter-separator;
    }

    &__arrow {
        @extend .slider-inner__arrow;

        &_left {
            @extend .slider-inner__arrow_left;
        }

        &_right {
            @extend .slider-inner__arrow_right;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slider-blocks {
        height: rem(442);
        margin: rem(58) 0 rem(70);

        &__text {
            padding-right: rem(10);
        }

        &__name {
            font-size: rem(32);
            line-height: rem(38);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slider-blocks {
        // height: rem(314);
        margin: 66px 0;

        &__pos {
            margin: 10px 0;
        }

        &__name {
            font-size: 32px;
            line-height: 38px;
        }

        &__text {
            bottom: 20px;
            left: 20px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .slider-blocks {
        height: rem(375);
        margin: 40px 0;

        &__text {
            padding-right: rem(50);
        }

        &__pos {
            font-size: 14px;
            line-height: 24px;
        }

        &__name {
            font-size: 26px;
            line-height: 31px;
        }
    }
}
