// constant name's variable for iOS 11, env for iOS 11+
:root {
    --app-height: 100vh;
    --app-inset-top: 0;
    --app-inset-bottom: 0;
    --app-inset-top: constant(safe-area-inset-top);
    --app-inset-top: env(safe-area-inset-top);
    --app-inset-bottom: constant(safe-area-inset-bottom);
    --app-inset-bottom: env(safe-area-inset-bottom);
}

html {
    font-size: .9375vw;
}

body {
    font-family: $font-primary;
    color: #fff;
    line-height: 1;
    // font-size: rem(14px);
    font-size: 14px;
}

.scroll-lock {
    overflow: hidden!important;
    height: 100%!important;
}

@media screen and (max-width: $laptop) {
    html { font-size: 1.25vw; }
}

// @media screen and (max-width: $tablet-landscape) {
//     html { font-size: 1.5625vw; }
// }

@media screen and (max-width: $tablet-portrait) {
    html { font-size: 2.34375vw; }
}

@media screen and (max-width: $mobile) {
    html { font-size: 5.625vw; }
}
