.product-icon {
    position: relative;
    padding-left: rem(112);

    &__svg {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        @extend %circle;
        background: $skinny;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__type {
        @extend %text-size;
        line-height: rem(40);
        color: #F0EDE5;
    }

    &__value {
        font-family: $font-secondary;
        font-size: rem(30);
        line-height: rem(36);
    }
}

@media screen and (max-width: $laptop) {
    .product-icon {
        padding-left: 90px;

        &__svg {
            width: 65px;
            height: 65px;
        }

        &__type {
            line-height: rem(32);
        }

        &__value {
            font-size: rem(28);
            line-height: rem(30);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .product-icon {
        padding-left: 0;

        &__svg {
            position: relative;
            top: 0;
            transform: none;
        }

        &__type {
            line-height: 19px;
            margin-top: 10px;
        }

        &__value {
            font-size: 28px;
            line-height: 34px;
            margin-top: 10px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .product-icon {
        &__svg {
            width: 45px;
            height: 45px;
        }

        &__type {
            line-height: 16px;
        }

        &__value {
            font-size: 18px;
            line-height: 22px;
            margin-top: 5px;
        }
    }
}
