.modal-people {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1001;
    display: none;
    opacity: 0;
    transition: opacity .3s ease;

    &__close {
        position: absolute;
        top: 52px;
        right: 50px;
        width: 58px;
        height: 58px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        border: 2px solid #E1D9C6;
        cursor: pointer;
        background: #F8F5EF;
        z-index: 2;
    }

    &__wnd {
        position: relative;
        width: rem(1760);
        background: #F8F5EF;
        box-shadow: 0px 0px 20px rgba(207, 203, 194, 0.2);
        transform: scale(.8);
        transition: transform .3s cubic-bezier(0.9, 0.18, 0.12, 0.91);
    }

    &__container {
        display: flex;
        overflow: hidden;
    }

    &__img {
        flex: 0 0 rem(695);

        img {
            display: block;
        }
    }

    &__content {
        width: 100%;
        position: relative;
    }

    &__content-wrp {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        // mask-image: linear-gradient(180deg,transparent,#000 0%,#000 70%,transparent 90%, transparent);

        &::-webkit-scrollbar-thumb {
            background: $gray;
        }

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }

        &::-webkit-scrollbar-track {
            background: #fff;
        }
    }

    &__content-scroll {
        padding: rem(81) rem(149) rem(140) rem(140);
    }

    &__slider-controls {
        position: absolute;
        bottom: rem(50);
        right: 50px;
        z-index: 2;
    }

    &__slider-controls-container {
        display: flex;
        justify-content: space-between;
        width: 207px;
    }

    &__slider-arrow {
        position: relative;
        outline: none;
        cursor: pointer;

        &_next {
            padding-right: 42px;

            .modal-people {
                &__slider-arrow-icon {
                    right: 0;
                }
            }
        }

        &_prev {
            padding-left: 42px;

            .modal-people {
                &__slider-arrow-icon {
                    left: 0;
                }
            }
        }

        &.swiper-button-disabled {
            opacity: .5;
        }
    }

    &__slider-arrow-label {
        @extend %text-size;
        color: $dark;
    }

    &__slider-arrow-icon {
        position: absolute;
        top: 30%;
        transform: translate3d(0, -50%, 0);
    }

    &_open {
        display: flex;
    }

    &_animated {
        opacity: 1;

        .modal-people {
            &__wnd {
                transition-timing-function: cubic-bezier(0.14, 0.51, 0.34, 1.06);
                transform: none;
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .modal-people {
        &__wnd {
            width: rem(1320);
        }

        &__img {
            flex-basis: rem(541);
        }

        &__close {
            right: 40px;
            top: 40px;
        }

        &__content-scroll {
            padding: rem(71) rem(159) rem(140) rem(87);
        }

        &__slider-controls {
            bottom: rem(37px);
            right: 40px;
        }

        &__content-wrp {
            // mask-image: linear-gradient(180deg,transparent,#000 0%,#000 60%,transparent 80%, transparent);
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .modal-people {
        
    }
}

@media screen and (max-width: $tablet-portrait) {
    .modal-people {
        &__wnd {
            width: 85%;
            height: 70%;
        }

        &__img {
            display: none;
        }

        &__slider {
            height: 100%;
        }

        &__slider-container {
            height: 100%;
        }

        &__slider-arrow-icon {
            top: 40%;

            svg {
                width: 21px;
                height: 7px;
            }
        }

        &__container {
            height: 100%;
        }

        &__content-wrp {
            position: relative;
        }

        &__content-scroll {
            padding: rem(80) rem(50) rem(80) rem(50);
        }

        &__content-wrp {
            // mask-image: linear-gradient(180deg,transparent,#000 0%,#000 80%,transparent 90%, transparent);
        }
    }
}

@media screen and (max-width: $mobile) {
    .modal-people {
        &__wnd {
            width: 100%;
            height: 100%;
        }

        &__close {
            width: 36px;
            height: 36px;
            right: 20px;
            top: 20px;

            svg {
                width: 13px;
                height: 13px;
            }
        }

        &__content-scroll {
            padding: rem(32) rem(20) rem(80) rem(20);
        }

        &__slider-controls {
            bottom: 20px;
            right: 50%;
            transform: translate3d(50%, 0, 0);
        }

        &__slider-arrow-icon {

            svg {
                width: 18px;
                height: 5px;
            }
        }

        &__slider-controls-container {
            width: 161px;
        }

        &__slider-arrow {

            &_next {
                padding-right: 30px;
            }

            &_prev {
                padding-left: 30px;
            }
        }

        &__text {
            h4 {
                font-size: 14px;
                line-height: 28px;
            }

            .page-title {
                font-size: 26px;
                line-height: 31px;
            }
        }
    }
}
