.modal-gallery {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    z-index: 1010;
    display: none;
    opacity: 0;
    transition: opacity .3s ease;
    background: $dark;

    &__close {
        position: absolute;
        top: 50px;
        right: 50px;
        width: 58px;
        height: 58px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        border: 2px solid $gold;
        cursor: pointer;
        background: $dark;
        z-index: 2;
    }

    &__wnd {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 115px 206px;
        box-shadow: 0px 0px 20px rgba(207, 203, 194, 0.2);
        transform: scale(.8);
        transition: transform .3s cubic-bezier(0.9, 0.18, 0.12, 0.91);
    }

    &__container {
        // display: flex;
        // overflow: hidden;
    }

    &__img {
        position: relative;
        overflow: hidden;
        height: 100%;

        img {
            // display: block;
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        width: 100%;
        position: relative;
    }

    &__content-wrp {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        // mask-image: linear-gradient(180deg,transparent,#000 0%,#000 70%,transparent 90%, transparent);

        &::-webkit-scrollbar-thumb {
            background: $gray;
        }

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }

        &::-webkit-scrollbar-track {
            background: #fff;
        }
    }

    &__slider-controls {
        position: absolute;
        bottom: rem(50);
        left: 50%;
        z-index: 2;
        transform: translate3d(-50%, 0, 0);
    }

    &__slider-controls-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 157px;
    }

    &__slider-arrow {
        position: relative;
        outline: none;
        cursor: pointer;

        &_next {
            padding-right: 42px;

            .modal-people {
                &__slider-arrow-icon {
                    right: 0;
                }
            }
        }

        &_prev {
            padding-left: 42px;

            .modal-people {
                &__slider-arrow-icon {
                    left: 0;
                }
            }
        }

        &.swiper-button-disabled {
            opacity: .5;
        }
    }

    &__slider-arrow-label {
        @extend %text-size;
        color: $dark;
    }

    &__slider-arrow-icon {
        position: absolute;
        top: 30%;
        transform: translate3d(0, -50%, 0);
    }

    &__counter {
        // display: none;
    }

    &__counter-current {
        @extend .slider-inner__counter-current;
        color: #fff;
    }

    &__counter-count {
        @extend .slider-inner__counter-count;
        color: #fff;
    }

    &__counter-separator {
        @extend .slider-inner__counter-separator;
        color: #fff;
    }

    &_open {
        display: block;
    }

    &_animated {
        opacity: 1;

        .modal-gallery {
            &__wnd {
                transition-timing-function: cubic-bezier(0.14, 0.51, 0.34, 1.06);
                transform: none;
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .modal-gallery {
        &__wnd {
            padding: 175px 120px 145px;
        }

        &__slider-controls {
            bottom: 35px;
        }

        &__img {
            overflow: hidden;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .modal-gallery {
        &__close {
            right: 60px;
        }

        &__wnd {
            padding: 175px 60px 145px;
            display: flex;
            align-items: center;
        }

        &__slider {
            width: 100%;
        }
    }
}

@media screen and (max-width: $mobile) {
    .modal-gallery {
        &__close {
            width: 36px;
            height: 36px;
            top: 20px;
            right: 20px;

            svg {
                width: 13px;
                height: 13px;
            }
        }

        &__wnd {
            padding: 80px 20px 80px;
        }

        &__slider-controls {
            bottom: 20px;
        }

        &__slider-controls-container {
            width: 120px;
        }

        &__slider-arrow {

            svg {
                width: 19px;
                height: 8px;
            }
        }
    }
}
