.container {
    // padding: 0 rem(80);
    @extend %padding-left;
    @extend %padding-right;

    &_relative {
        position: relative;
    }

    &_full-height {
        height: 100%;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .container {
        &_full-height {
            height: auto;
        }
    }
}
