$main-font-size: rem(28px);

$colors: (beige: #F1EDE5,
        white: #fff,
        black: #2E2E2E,
        lightBeige: #BEBBB6,
);

.mg-bottom-lg {
        margin-bottom: 500px;
}

.mg-top-lg {
        margin-top: 200px;

        @media screen and(max-width: 1366px) {
                margin-top: 150px;
        }

        @media screen and(max-width: 765px) {
                margin-top: 100px;
        }
}

.mg-top-sm {
        margin-top: 60px;

        @media screen and(max-width: 765px) {
                margin-top: 40px;
        }
}

.mg-top-l {
        margin-top: 150px;

        @media screen and(max-width: 1024px) {
                margin-top: 100px;
        }

        @media screen and(max-width: 765px) {
                margin-top: 60px;
        }
}

.mg-top-md {
        margin-top: 100px;

        @media screen and(max-width: 765px) {
                margin-top: 40px;
        }
}


.line {
        height: 2px;
        background-color: map-get($colors, black);
        margin-left: 24px;
        margin-right: 7px;

        &--1 {
                width: 130px;
                margin-left: 0;

                @media screen and(max-width: 1366px) {
                        width: 57px;
                }
        }

        &--2 {
                width: 46px;
                margin-right: 8px;
                margin-left: 8px;
        }

        &--3 {
                width: 92px;

                @media screen and(max-width: 1366px) {
                        width: 57px;
                }

        }
}

.small-text {
        font-size: 28px;
        -webkit-text-fill-color: map-get($colors, black);
        display: inline-flex;
        align-items: center;
        text-transform: none;
        line-height: 27.3px;

        @media screen and (max-width: 1366px) {
                font-size: 24px;
                line-height: 24px;
        }

        @media screen and (max-width: 765px) {
                line-height: 18px;
                font-size: 18px;
        }

        svg {
                margin-right: 24px;
        }
}

.n-subtitle-top {
        -webkit-text-fill-color: map-get($colors, black);
        display: flex;
        align-items: center;
        transform: translateY(-218px);
        grid-column-start: 6;
        
        @media screen and (max-height: 767px) {
                
                transform: translateY(-100px);
        }
        @media screen and (min-height: px) {
                
                transform: translateY(-150px);
        }
        
        @media screen and (max-width: 765px) {
                grid-column-start: 4;
                
        }

        svg {
                margin-right: 24px;
        }
}

.large-text {
        font-size: 84px;
        text-transform: uppercase;
        line-height: 76px;

        @media screen and (max-width: 1366px) {
                font-size: 60px;
                line-height: 60px;
        }

        @media screen and (max-width: 765px) {
                font-size: 40px;
                line-height: 40px;
        }

        p {
                display: inline;

        }

        svg {
                margin-left: 24px;
        }


        .n-subtitle {
                transform: translateY(-75%);

                @media screen and (max-width: 765px) {
                        transform: translateY(-40%);
                }
        }

}



.main {
        grid-row: 2;
        display: grid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;

        img {
                width: 100%;
        }
}


.new-page-body {
        background: map-get($colors, beige);
        color: map-get($colors, black);
        font-family: $font-secondary;
        font-size: $main-font-size;
}


.layout-container {
        display: grid;
        max-width: 100vw;
        overflow: hidden;
        grid-template-columns: .9fr 1fr 4fr 1.5fr 1.5fr 4fr 1fr .9fr;
        // grid-template-columns: 5vw 5vw 30vw 15vw 15vw 30vw 5vw 5vw;

        @media screen and (max-width: 1600px) {
                grid-template-columns: 1fr 2fr 7.5fr 4fr 4fr 7.5fr 2fr 1fr;
        }


        @media screen and (max-width: 1024px) {
                grid-template-columns: 1.3fr 1.3fr 5fr 1.8fr 1.8fr 5fr 1.3fr 0fr;
        }

        @media screen and (max-width: 765px) {
                grid-template-columns: .55fr 0fr 4.5fr 2fr .5fr 5fr 0fr .55fr;
        }
}

.content {
        grid-row: 2;
        display: grid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;
}

.running-line {
        grid-column-start: 1;
        grid-column-end: 9;
}


.hero {
        // transform: translateY(100vh);
        -webkit-text-fill-color: map-get($colors, black);
        height: 100vh;
        align-items: start;
        display: grid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;
}

.n-subtitle-2 {
        grid-column-start: 3;

        @media screen and (max-width: 1024px) {
                grid-column-start: 2;
                grid-column-end: 4;
                margin-bottom: 60px;
        }

        @media screen and(max-width: 765px) {
                grid-column-end: 6;
                margin-bottom: 0px;
        }
}

.n-subtitle {
        grid-column-start: 6;
        max-width: 315px;

        @media screen and (max-width: 765px) {
                grid-column-start: 4;
                grid-column-end: 7;
        }

}

.n-subtitle-7 {
        margin-top: 267px;
        grid-column-start: 6;
        max-width: 315px;
}

.title {
        grid-column-start: 4;
        grid-column-end: 7;
        margin-top: 40px;


        @media screen and(max-width: 1024px) {
                grid-column-start: 2;
        }

        @media screen and (max-width: 765px) {
                grid-column-end: 7;
        }
}


.title-2 {
        grid-column-start: 3;
        grid-column-end: 8;
        // max-width: 1160px;

        @media screen and (max-width: 1366px) {
                svg {

                        rect {
                                width: 54px;
                        }
                }
        }

        @media screen and(max-width: 1024px) {
                grid-column-start: 2;
                grid-column-end: 7;
        }
}

.numbers {
        grid-column-start: 4;
        display: flex;
        align-items: center;
        font-size: 28px;

        @media screen and (max-width: 1366px) {
                font-size: 24px;
        }

        @media screen and (max-width: 1024px) {
                grid-column-start: 6;
                margin-bottom: 60px;
        }
        
        @media screen and (max-width: 765px) {
                margin-bottom: 0px;
                font-size: 18px;
                justify-self: end;

        }

        
}

.links-wrapper {
        grid-column-start: 1;
        width: 100%;
        grid-column-end: 9;
        display: grid;
        grid: inherit;
        margin-bottom: 40px;

        @media screen and(max-width: 765px) {
                margin-bottom: 20px;
        }

        a:first-child {
                grid-column-start: 3;
        }

        a:last-child {
                grid-column-start: 4;

                @media screen and (max-width: 765px) {
                        grid-column-start: 5;
                }
        }

        a {
                color: inherit;
                text-decoration: none;
        }
}

.n-subtitle-5 {
        grid-column-start: 6;
        margin-top: 60px;

        @media screen and (max-width: 765px) {
                grid-column-start: 5;
                grid-column-end: 7 !important;
                justify-self: start !important;
        }
}

.n-subtitle-6 {
        grid-column-start: 3;
        margin-top: 200px;
}

.n-subtitle-5 {
        grid-column-start: 4;
        grid-column-end: 6;
        justify-self: center;
}

.title-3 {
        grid-column-start: -4;
        grid-column-end: -7;
        justify-self: end;

        @media screen and (max-width: 1024px) {
                grid-column-start: 2;
                grid-column-end: 7;
                justify-self: start;
        }
}

.images-container {
        grid-column-start: 1;
        width: 100%;
        grid-column-end: 9;
        display: flex;
        align-items: flex-start;


        &__wrapper {
                overflow: hidden;
                // height: 100%;

                &:first-child {
                        img {
                                width: 23vw;
                
                                @media screen and (max-width: 765px) {
                                        width: 35vw;
                                }
                        }
                }

                &:last-child {
                        img:last-child {
                                width: 42vw;
                
                                @media screen and (max-width: 765px) {
                                        width: 65vw;
                                }
                        }
                }
        }
        
        img {
                transition: all .3s;
                height: auto;
                &:hover {
                        transform: scale(1.1);
                        filter: brightness(70%);
                }
        }



        

        
}

.image-link {
        font-size: 28px;

        @media screen and (max-width: 1366px) {
                font-size: 24px;
        }
}

.offers {
        grid-column-start: 1;
        grid-column-end: 9;
        display: grid;
        grid: inherit;

        &__link-wrapper {}


        &__text {
                hyphens: auto;
                padding: 26px 0;
                word-break: break-all;

                @media screen and (max-width: 1440px) {
                        padding: 6px 0px;
                }
                @media screen and (max-width: 1024px) {
                        padding: 4px 0px;
                }
        }





        &__slash {
                margin: 0 16px;

                @media screen and (max-width: 1024px) {
                        display: none;
                }
        }

        &__item {

                border-top: 2px solid #BEBBB6;
                border-bottom: 2px solid #BEBBB6;
                grid-column-start: 3;
                grid-column-end: 7;
                display: flex;
                align-items: center;
                cursor: pointer;
                // transition: all 0.3s;
                transform-origin: left;
                width: 100%;
                margin-top: -2px;

                


                img {
                        height: 128px;
                        margin-left: 24px;
                        margin-right: 24px;
                        width: 0;
                        transition: all .3s;
                        display: none;

                        @media screen and (max-width: 1440px) {
                                height: 86px;
                        }

                        @media screen and (max-width: 1200px) {
                                height: 72px;
                                margin-right: 12px;
                                margin-left: 12px;
                        }

                        @media screen and (max-width: 765px) {
                                margin-left: 9px;
                                margin-right: 9px;
                                height: 48px;
                        }
                }

                @media screen and (max-width: 1024px) {
                        grid-column-start: 2;
                        grid-column-end: 7;
                }

                @media screen and (max-width: 1024px) {
                        border-top: 2px solid map-get($colors, black);
                        border-bottom: 2px solid map-get($colors, black);
                        z-index: 9;

                        .offers__link {
                                color: map-get($colors, black);
                        }

                        .offers__slash {
                                display: none;
                        }

                        img {
                                display: block;
                                width: auto;
                        }

                        &:hover {
                                width: 100%;
                        }
                }

                &:hover {
                        width: 107%;
                        border-top: 2px solid map-get($colors, black);
                        border-bottom: 2px solid map-get($colors, black);
                        z-index: 9;

                        .offers__link {
                                color: map-get($colors, black);
                        }

                        .offers__slash {
                                display: none;
                        }

                        img {
                                display: block;
                                width: auto;
                        }

                        @media screen and (max-width: 1024px) {}
                }
        }


        &__link {
                color: #BEBBB6;
                text-decoration: none;
                display: inline-flex;
                align-items: center;
                text-transform: uppercase;
                flex-wrap: wrap;
                transition: all .3s;



                @media screen and (max-width: 1780px) {
                        font-size: 60px !important;
                }

                @media screen and (max-width: 1175px) {
                        font-size: 40px !important;
                }

                @media screen and (max-width: 1024px) {
                        font-size: 30px !important;
                }

                @media screen and (max-width: 765px) {
                        font-size: 24px !important;
                }
        }
}

.container-1 {
        max-width: calc(1440px - 20px);
        padding: 0 20px;
        margin: 0 auto;
        width: 100%;
        -webkit-text-fill-color: map-get($colors, black);
}

.gallery {
        display: flex;
        flex-wrap: wrap;
        grid-column-start: 1;
        grid-column-end: 9;
        max-width: 100vw;
        overflow: hidden;

        &__flex {
                display: flex;
                align-items: start;
                overflow: hidden;
                min-width: 1200px;
        }

        &__item-wrapper {
                overflow: hidden;
                height: 100%;
        }


        img {
                object-fit: cover;
                height: 100%;
                // width: auto;
                max-width: 100%;
                transition: all .3s;

                // @media screen and (max-width: 1919px ) {
                //         max-width: 100%;
                // }

        }

        &__item {
                position: relative;
                color: map-get($colors, white);
                flex: 2 0 33.33333%;

                &:hover {
                        img {
                                transform: scale(1.1);
                                filter: brightness(70%);
                        }
                }

                @media screen and (max-width: 765px) {
                        min-width: 100vw;
                        flex: 1;
                }

                &--slider {
                        min-width: 25%;
                        flex: auto;

                        @media screen and (max-width: 500px) {
                                min-width: 15%;
                        }
                }



                &--small {
                        flex: 1 0 21%;

                        @media screen and (max-width: 768px) {
                                flex: 0 0 304px;
                        }

                        @media screen and (max-width: 765px) {
                                min-width: 50vw;
                                flex: 1;
                        }

                        img {
                                height: auto;

                                @media screen and (max-width: 768px) {
                                        height: 100%;
                                }

                        }

                }
        }

        &__item-name {
                padding: 36px;
                position: absolute;
                z-index: 1;
                top: 0;

                font-size: 28px;

                @media screen and (max-width: 1366px) {
                        font-size: 24px;
                }

                @media screen and(max-width: 765px) {
                        padding: 30px 20px;
                }
        }

        &__flex-item {
                text-decoration: none;

                img {
                        width: auto;
                }
        }

        &__text {
                padding-top: 48px;
                padding-left: 100px;
                color: map-get($colors, black);
                font-size: 29px;

                @media screen and (max-width: 1366px) {
                        font-size: 24px;
                        padding-top: 36px;
                        padding-left: 36px;

                }

                @media screen and (max-width: 765px) {
                        font-size: 18px;
                        padding-top: 30px;
                        padding-left: 30px;

                }

                p:first-child {
                        margin-bottom: 24px;
                }
        }


}

.grid-sec {
        height: auto;
        display: grid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;
}

.n-subtitle-4 {
        grid-column-start: 4;
        grid-column-end: 6;

        @media screen and (max-width: 1024px) {
                grid-column-start: 1;
                grid-column-end: 9;
                justify-self: center;

        }
}

.n-subtitle-9 {
        margin-top: 100px;
        grid-column-start: 6;

}

.last-img {
        grid-column-start: 6;
        width: 30vw;
        height: auto;

        @media screen and (max-width: 765px) {
                grid-column-start: 5;
                grid-column-end: 8;
        }

}

.active {
        img {
                opacity: 1;
        }
}

.header {
        width: 100%;
        // display: flex;
        // padding: rem(13px) rem(40px);
        border-bottom: 1px solid #BEBBB6;
        align-items: center;
        position: fixed;
        top: 0;
        background-color: map-get($colors, beige);
        z-index: 99;
        display: grid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;




        &__logo-container {
                padding: 15px 40px 11px 40px;

                @media screen and (max-width: 1600px) {
                        padding: 15px 40px 11px 0px;
                        grid-column-start: 2;
                }

                @media screen and (max-width: 1600px) {
                        padding-top: 30px;
                        padding-bottom: 30px;
                }

                @media screen and (max-width: 1600px) {
                        padding-top: 20px;
                        padding-bottom: 20px;
                }

                img {
                        height: 40px;
                        width: 50px;

                        @media screen and (max-width: 765px) {
                                height: 27.3px;
                                width: 34px;
                        }
                }
        }

        &__logo {
                height: rem(42px);
                width: rem(34px);

        }
}

.new-page-menu {
        grid-column-start: 3;
        grid-column-end: 7;
        display: flex;
        justify-content: space-between;

        &__item {}

        &__link {
                text-decoration: none;
                display: flex;
                align-items: baseline;
                color: map-get($colors, black);
                font-size: 21px;

                svg {
                        margin-left: 11px;
                }
        }

        @media screen and (max-width: 768px) {
                display: none;
        }
}

.menu-trigger {
        width: 48px;
        cursor: pointer;
        grid-column-start: 6;
        justify-self: end;
        display: none;

        @media screen and (max-width: 765px) {
                width: 34px;
        }

        &__line {
                height: 2px;

                &--top {
                        display: flex;

                        div {
                                background-color: map-get($colors, black);

                                &:first-child {
                                        width: 60%;
                                }

                                &:last-child {
                                        width: 30%;
                                        margin-left: auto;
                                }
                        }

                        margin-bottom: 5px;
                }

                &--bottom {
                        background-color: map-get($colors, black);
                        width: 100%;
                }
        }

        @media screen and (max-width: 768px) {
                display: block;
        }
}


.parallax {
        background-image: url("/images/new-page/hero-bg.jpg");
        // min-height: 300vh;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        // position: absolute;
        // top: 0;left: 0;
        // height: 300vh;
        width: 100vw;
        display: grid;
        grid-template-columns: subgrid;
        grid: inherit;
        grid-column-start: 1;
        grid-column-end: 9;

        color: #fff;
        /* -webkit-background-clip       */
        -webkit-text-fill-color: transparent;
        /*           webkit */
        -webkit-background-clip: text;
        background-clip: text;
}

.running-line {
        text-transform: uppercase;
        max-width: 100vw;
        overflow: hidden;
        height: 100vh;
        display: flex;
        align-items: center;

        &__wrapper {
                min-width: 3500px;
        }

        &__text {
                font-size: 450px;
                font-weight: 600;
                letter-spacing: 20px;

                @media screen and (max-width:1440px) {
                        font-size: 325px;
                }

        }
}

.overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: map-get($colors, beige);
        z-index: -1;
}

.text-p {
        font-size: 28px;
        line-height: 27.3px;

        @media screen and (max-width: 1366px) {
                font-size: 24px;
        }
}