.block-media {
    margin-top: rem(164);

    &:first-child {
        // margin-top: 0;
    }

    &__container {
        position: relative;
    }

    &__img {
        position: absolute;

        &_top {
            bottom: auto;
            top: 0;
        }
    }

    &__text-title {
        padding: 0 rem(700) 0 rem(243);
        font-family: $font-secondary;
        font-size: rem(48);
        line-height: rem(58);
        margin-bottom: rem(77);
        color: $dark;
    }


    &__img-big {
        position: absolute;

        &_top {
            bottom: auto;
            top: 0;
        }
    }

    &__img-small, &__img-small-left, &__img-small-bottom {
        position: absolute;
    }

    &__title {
        font-family: $font-secondary;
        font-size: rem(48);
        line-height: rem(58);
        margin-bottom: rem(77);
        color: $dark;
    }

    &__text {
        position: relative;
    }

    &__pattern {
        display: none;
        position: absolute;
        width: rem(597);
        height: rem(395);
        top: rem(-121);
        left: rem(-332);
        z-index: -1;
        @extend %pattern-yellow;

        &::before {
            @extend %pseudo;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: radial-gradient(50% 50% at 50% 50%, rgba(241, 237, 229, 0) 0%, rgba(241, 237, 229, 0.8) 100%);
        }
    }

    &__body {
        @extend .text;
    }

    &__tasting {
        position: relative;

        &-img {
            max-width: 100%;
        }
    }

    &_type {
        &_default {
            margin-bottom: rem(460);

            .block-media {
                &__container {
                    padding: 0 rem(700) 0 rem(243);
                }

                &__img {
                    right: 0;
                    // top: rem(60);
                    bottom: 0;
                }

                &__img-big {
                    right: 0;
                    // top: 0;
                    bottom: rem(-200);
                    width: rem(590);
                }

                &__img-small {
                    right: rem(663);
                    bottom: rem(-341);
                    width: rem(258);
                }
            }
        }

        &_brending {
            margin-bottom: rem(130);
        }

        &_default-lifted {
            @extend .block-media_type_default;
            margin-bottom: rem(360);

            .block-media {
                &__img-big {
                    bottom: 0;
                }

                &__img-small {
                    bottom: rem(-150);
                }
            }
        }

        &_default-tripple {
            margin-bottom: rem(714);

            .block-media {
                &__container {
                    padding: 0 rem(800) 0 rem(243);
                }

                &__img {
                    right: 0;
                    top: rem(60);
                    bottom: 0;
                }

                &__img-big {
                    right: 0;
                    bottom: rem(-230);
                    width: rem(628);
                }

                &__img-small-left {
                    right: rem(628 + 80);
                    bottom: rem(-230 - 190);
                    width: rem(417);
                }

                &__img-small-bottom {
                    right: rem(628 - 417);
                    bottom: rem(-230 - 273 - 80);
                    width: rem(417);
                }
            }
        }

        &_inverse {
            margin-bottom: rem(258);

            .block-media {
                &__container {
                    padding: 0 rem(212) 0 rem(841);
                }

                &__img {
                    left: 0;
                    // top: rem(60);
                    top: rem(-30);
                    bottom: 0;
                }

                &__img-big {
                    left: rem(305);
                    // top: rem(210);
                    bottom: rem(-110);
                    width: rem(417);
                }

                &__img-small {
                    left: 0;
                    // top: 0;
                    bottom: rem(270);
                    width: rem(237);
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .block-media {
        margin-top: rem(141);

        &__text-title {
            padding: 0 0 0 rem(113);
            font-family: $font-secondary;
            font-size: rem(32);
            line-height: rem(38);
            margin-bottom: rem(50);
            color: $dark;
        }

        &__title {
            font-size: rem(32);
            line-height: rem(38);
            margin-bottom: rem(50);
            max-width: rem(480);
        }

        &_type {
            &_default {
                margin-bottom: rem(373);

                .block-media {
                    &__container {
                        padding: 0 rem(566) 0 rem(116);
                    }

                    &__img {
                        top: rem(60);
                    }

                    &__img-big {
                        width: rem(485);
                        bottom: rem(-150);
                    }

                    &__img-small {
                        right: rem(531);
                        bottom: rem(-280);
                        width: rem(213);
                    }
                }
            }

            &_default-lifted {
                margin-bottom: rem(300);

                .block-media {
                    &__img-big {
                        bottom: 0;
                    }

                    &__img-small {
                        bottom: rem(-150);
                    }
                }
            }

            &_default-tripple {
                margin-bottom: rem(600);

                .block-media {
                    &__container {
                        padding: 0 rem(566) 0 rem(116);
                    }

                    &__title {
                        max-width: rem(500);
                    }

                    &__img-big {
                        bottom: rem(-210);
                        width: rem(500);
                    }

                    &__img-small-left {
                        right: rem(500 + 60);
                        bottom: rem(-210 - 160);
                        width: rem(350);
                    }

                    &__img-small-bottom {
                        right: rem(500 - 350);
                        bottom: rem(-210 - 220 - 60);
                        width: rem(350);
                    }
                }
            }

            &_inverse {
                margin-bottom: rem(154);

                .block-media {
                    &__container {
                        padding: 0 rem(0) 0 rem(683);
                    }

                    &__img {
                        top: rem(50);
                    }

                    &__img-big {
                        left: rem(248);
                        top: rem(95);
                        width: rem(351);
                    }

                    &__img-small {
                        width: rem(199);
                        top: rem(-70);
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .block-media {
        margin-top: rem(67);

        &__title {
            font-size: 32px;
            line-height: 38px;
            margin-bottom: rem(35);
            max-width: rem(550);
        }

        &__text-title {
            padding: 0;
            font-family: $font-secondary;
            font-size: 32px;
            line-height: 38px;
            margin-bottom: rem(35);
            color: $dark;
        }

        &__img {
            display: none;
        }

        &_type {
            &_default {
                margin-bottom: rem(44);

                .block-media {
                    &__container {
                        padding: 0;
                    }
                }
            }

            &_default-lifted {
                margin-bottom: rem(300);

                .block-media {
                    &__img-big {
                        bottom: 0;
                    }

                    &__img-small {
                        bottom: rem(-150);
                    }
                }
            }

            &_default-tripple {
                margin-bottom: 65px;

                .block-media {
                    &__container {
                        padding: 0;
                    }

                    &__title {
                        max-width: rem(550);
                    }
                }
            }

            &_inverse {
                margin-bottom: rem(44);

                .block-media {
                    &__container {
                        padding: 0;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .block-media {
        margin-top: 40px;

        &__title {
            font-size: 26px;
            line-height: 31px;
            margin-bottom: 25px;
        }

        &__text-title {
            padding: 0;
            font-family: $font-secondary;
            font-size: 26px;
            line-height: 31px;
            margin-bottom: 25px;
            color: $dark;
        }

        &_type {
            &_default {
                margin-bottom: 40px;
            }

            &_default-tripple {
                margin-bottom: 40px;
            }

            &_inverse {
                margin-bottom: 40px;
            }
        }
    }
}
