.title-subsection {
    @extend .contacts-layout__label;
}

@media screen and (max-width: $laptop) {
    .title-subsection {
        font-size: 32px;
        line-height: 38px;
    }
}

@media screen and (max-width: $mobile) {
    .title-subsection {
        font-size: 26px;
        line-height: 31px;
        margin-bottom: 40px;
    }
}
