.dropdown {
    position: relative;

    &__btn {
        position: relative;
        padding-right: 19px;
        cursor: pointer;
    }

    &__icon {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate3d(0, -50%, 0);

        svg {
            transition: transform .5s ease;
        }

        path {
            stroke: white;
        }
    }

    &__container {
        width: 250px;
        margin-top: 15px;
        position: absolute;
        top: 40px;
        padding: 30px;
        opacity: 0;
        pointer-events: none;
        transition: opacity .3s ease;
        background: $skinny;
        border-radius: 5px;
        box-shadow: 0px 0px 20px rgba(181, 174, 161, 0.2);
        transition: all .4s ease;
    }

    &__item {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link {
        color: $dark;
        transition: all .4s ease;
        line-height: 22px;

        &:hover {
            color: $green;
            transition: all .4s ease;
        }
    }

    &_open {
        .dropdown {
            &__container {
                opacity: 1;
                pointer-events: all;
                top: 100%;
                transition: all .4s ease;
            }

            &__icon {

                svg {
                    transform: rotate(180deg);
                }
            }
        }
    }
}
