.browsers {
    &__inner {}

    &__list {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__item {
        width: rem(50); height: rem(50);
        margin: 0 rem(15);
        cursor: pointer;

        * { display: block; }

        &:last-of-type { margin-right: 0; }
    }
}
