.social-icons {
    &__container {
        @extend .text-icons__container;
    }

    &__icon {
        @extend .text-icons__icon;
        width: 58px;
        height: 58px;
        margin-right: 31px;
        margin-bottom: 0;
    }

    &__label {
        @extend %text-size;
        line-height: 19px;
        color: $gray;
        margin-bottom: 20px;
    }
}

@media screen and (max-width: $laptop) {
    .social-icons {
        
    }
}

@media screen and (max-width: $mobile) {
    .social-icons {

        &__icon {
            width: 42px;
            height: 42px;
            margin-right: 20px;
        }

        &__label {
            margin-bottom: 12px;
        }
    }
}
