.news {
    display: block;

    &:hover {
        .news {
            &__link {
                &::before { transform: translate3d(20%, -50%, 0); }
            }
        }
    }

    &__img {
        margin-bottom: rem(40);
    }

    &__title {
        margin: rem(40) 0 rem(20);
        font-family: $font-secondary;
        font-size: rem(30);
        line-height: rem(36);
        color: $dark;
    }

    &__text {
        @extend %text-size;
        line-height: 28px;
        color: $dark;
        margin: rem(20) 0 rem(15);
    }

    &__link {
        margin-top: rem(30);
        margin-bottom: 0;
    }
}

@media screen and (max-width: $laptop) {
    .news {
        &__img {
            margin-bottom: rem(20);
        }

        &__title {
            margin: rem(20) 0 rem(15);
            font-size: 28px;
            line-height: 34px;
        }

        &__text {
            margin: rem(15) 0 rem(15);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .news {
        &__img {
            margin-bottom: 20px;
        }

        &__title {
            font-size: 22px;
            line-height: 26px;
            margin-bottom: 15px;
        }

        &__text {
            display: none;
        }

        &__link {
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .news {
        &__title {
            font-size: 20px;
            line-height: 24px;
            margin: 20px 0 10px;
        }

        &__link {
            margin-top: 15px;
        }
    }
}
