%clearfix {
    &::after,
    &::before {
        content: " ";
        display: table;
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
    }

    &::after {
        clear: both;
    }
}

%pseudo {
    content: '';
    display: block;
    position: absolute;
}

%btn-normalize {
    border: none;
    background: none;
    outline: none;
}

%link-normalize {
    color: inherit;
    text-decoration: none;
}

%list-normalize {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

%dots-overflow {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

