%col-offset {
    margin-left: rem(243);
}

%video-cover {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

%text-size {
    font-size: 16px;
}

%padding-left {
    padding-left: rem(80);
}

%padding-right {
    padding-right: rem(80);
}

%absolute-left {
    left: rem(80);
}

%absolute-right {
    right: rem(80);
}

%circle {
    width: rem(73);
    height: rem(73);
}

@media screen and (max-width: $laptop) {
    %circle {
        width: 60px;
        height: 60px;
    }

    %padding-left {
        padding-left: 60px;
    }
    
    %padding-right {
        padding-right: 60px;
    }
    
    %absolute-left {
        left: 60px;
    }
    
    %absolute-right {
        right: 60px;
    }

    %col-offset {
        margin-left: rem(116);
    }
}

@media screen and (max-width: $tablet-landscape) {

    %padding-left {
        padding-left: 40px;
    }
    
    %padding-right {
        padding-right: 40px;
    }

    %absolute-left {
        left: 40px;
    }
    
    %absolute-right {
        right: 40px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    %circle {
        width: 68px;
        height: 68px;
    }

    %padding-left {
        padding-left: 60px;
    }

    %padding-right {
        padding-right: 60px;
    }

    %absolute-left {
        left: 60px;
    }

    %absolute-right {
        right: 60px;
    }

    %col-offset {
        margin-left: 0;
    }
}

@media screen and (max-width: $mobile) {
    %circle {
        width: 46px;
        height: 46px;
    }

    %padding-left {
        padding-left: 20px;
    }

    %padding-right {
        padding-right: 20px;
    }

    %absolute-left {
        left: 20px;
    }

    %absolute-right {
        right: 20px;
    }

    %text-size {
        font-size: 14px;
    }
}
