.drag-slider {
    transition: opacity .7s .2s ease, transform .5s ease;

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 60px;
        height: 194px;
        margin: 0 auto;
        position: relative;
    }

    &__gap {
        position: absolute;
        top: 50%;
        left: 50%;;
        transform: translate3d(-50%, -50%, 0);
        height: 140px;
        width: 4px;
    }

    &__connect {
        position: absolute;
        width: 100%;
        height: 100%;
        bottom: 10px;
        overflow: hidden;

        svg {
            position: absolute;
            top: 50%;
            transform: translate3d(0, -50%, 0);
        }
    }

    &__origin {
        width: 0;
        height: 0;
        position: absolute;

        &_left {
            top: 0;
            z-index: 1;
        }

        &_right {
            bottom: 0;

            .drag-slider {
                &__circle {

                    &::after {
                        @extend %pseudo;
                        top: 50%;
                        left: 50%;
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        border: 1px solid white;
                        transform: translate3d(-50%, -50%, 0);
                        transition: transform 1s cubic-bezier(0.06, 0.43, 0.25, 1);
                        opacity: 0;
                    }
                }
            }
        }
    }

    &__circle {
        font-family: $font-secondary;
        @extend %text-size;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        width: 73px;
        height: 73px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid #fff;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);

        &[data-behavior="drag"] {
            position: absolute;
            z-index: 1000;
            cursor: grab;
            border: none;
            display: flex;
            justify-content: center;
            align-items: center;
            // transform: translate3d(0, -50%, 0);

            &::before {
                @extend %pseudo;
                position: relative;
                background: #fff;
                width: 36px;
                height: 36px;
                border-radius: 50%;
                transition: transform .8s cubic-bezier(0.84, 0.2, 0.25, 1), opacity .3s cubic-bezier(0.84, 0.2, 0.25, 1);
            }

            &::after {
                @extend %pseudo;
                top: 50%;
                left: 50%;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                border: 1px solid white;
                opacity: 0;
                transition: transform .5s ease;
                transform: translate3d(-50%, -50%, 0);
            }
        }

        &[data-behavior="allow"] {
            line-height: normal;
            margin-left: auto;

            > svg {
                width: 60px;
                height: 60px;
            }
        }
    }

    &_disabled {
        opacity: 0;
        pointer-events: none;

        .drag-slider {
            &__circle {
                &[data-behavior="drag"] {
                    &::before {
                        transform: scale(0);
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .drag-slider {

        &__gap {
            height: 127px;
        }

        &__circle {

            &[data-behavior="drag"] {
                &::before {
                    width: 28px;
                    height: 28px;
                }
            }

            &[data-behavior="allow"] {
    
                > svg {
                    width: 60px;
                    height: 60px;
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .drag-slider {
        
    }
}

@media screen and (max-width: $tablet-portrait) {
    .drag-slider {
        &__circle {
            &[data-behavior="allow"] {
                font-size: 14px;

                > svg {
                    width: 68px;
                    height: 68px;
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .drag-slider {

        &__inner {
            height: 100px;
        }

        &__gap {
            height: 83px;
            width: 2px;
        }

        &__connect {
            bottom: 12px;
        }

        &__circle {
            width: 46px;
            height: 46px;

            &[data-behavior="drag"] {
                &::before {
                    width: 20px;
                    height: 20px;
                }
            }

            &[data-behavior="allow"] {
                font-size: 10px;
                line-height: 12px;

                > svg {
                    width: 46px;
                    height: 46px;
                }
            }
        }
    }
}
