.age-verification {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    transition: transform 2s cubic-bezier(.8,0,.14,1);
    background: $main-theme;
    overflow: hidden;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        width: 100%;
        height: 100%;
        @extend %pattern;
    }

    &__gradient {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(50% 50% at 50% 50%, rgba(35, 174, 163, 0) 0%, rgba(35, 174, 163, 0.2625) 26.56%, rgba(35, 174, 163, 0.598437) 63.02%, rgba(35, 174, 163, 0.8) 100%);
        display: none;
    }

    &__inner {
        position: relative;
        z-index: 1;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        user-select: none;
    }

    &__container {
        text-align: center;
    }

    &__denied {
        font-family: $font-secondary;
        font-size: 60px;
        line-height: 150%;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        padding: 0 40px;
        max-width: 950px;
    }

    &__title {
        font-family: $font-secondary;
        font-size: 82px;
        margin-bottom: rem(100);
        letter-spacing: 0.1em;
        overflow: hidden;
        transform: translate3d(0, 100%, 0);
        transition: transform 2s cubic-bezier(.8,0,.14,1);
    }

    &__title-text {
        display: inline-block;
        text-transform: uppercase;
        // transform: translate3d(0, -100%, 0);
        transition: transform 2s .2s cubic-bezier(.8,0,.14,1);
    }

    &__buttons {
        overflow: hidden;
        transform: translate3d(0, 100%, 0);
        transition: transform 2.1s cubic-bezier(.8,0,.14,1);
    }

    &__buttons-container {
        display: flex;
        justify-content: center;
        transition: transform 2.5s cubic-bezier(.8,0,.14,1);
    }

    &__btn {
        @extend %btn-normalize;
        font-family: $font-secondary;
        min-width: 200px;
        padding: 28px 0 30px;
        font-size: 36px;
        line-height: 42px;
        text-align: center;
        letter-spacing: 0.1em;
        color: #fff;
        background: #40BEDA;
        border: 1px solid transparent;
        text-transform: uppercase;
        cursor: pointer;
        transition: border .3s ease;

        &:nth-child(1) {
            margin-right: 80px;
        }

        &:hover {
            border: 1px solid #FFFFFF;
        }
    }

    &_ready {
        .age-verification {
            &__title {
                transform: none;
            }

            &__title-text {
                transform: none;
            }

            &__buttons {
                transform: none;
            }
        
            &__buttons-container {
                transform: none;
            }
        }

        .date-input {
            &__container {
                transform: none;
            }
        }
    }

    &_done {
        transform: translate3d(0, -100%, 0);

        .age-verification {
            &__title {
                transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
                transform: translate3d(0, 500%, 0);
            }

            &__title-text {
                transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
                transform: translate3d(0, 200%, 0);
            }

            &__buttons {
                transform: translate3d(0, 500%, 0);
                transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
            }
        
            &__buttons-container {
                transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
                transform: translate3d(0, 200%, 0);
            }
        }

        .date-input {
            transform: translate3d(0, 500%, 0);
            transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);

            &__container {
                transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
                transform: translate3d(0, 200%, 0);
            }
        }

        .drag-slider {
            &__circle {
                &::before {
                    opacity: 0;
                }

                &[data-behavior="drag"] {
                    &::after {
                        opacity: 1;
                        animation: border-width .5s ease forwards;
                        transform: translate3d(-50%, -50%, 0) scale(5);
                    }
                }
            }
        }
    }

    .no-spot {
        .age-verification {
            &__gradient {
                display: block;
            }
        }
    }
}

@keyframes border-width {
    from { border-width: rem(1); }
    15% { border-width: rem(7); }
    55% { border-width: rem(1); }
    to { border-width: rem(0); }
}

@media screen and (max-width: $laptop) {
    .age-verification {
        &__title {
            font-size: 60px;
            margin-bottom: 100px;
        }

        &__btn {
            min-width: 170px;
            padding: 26px 0 28px;
            font-size: 28px;
            line-height: 32px;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .age-verification {
        &__title {
            font-size: 52px;
            line-height: 60px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .age-verification {
        &__title {
            font-size: 26px;
            line-height: 30px;
            margin-bottom: 40px;
        }

        &__denied {
            font-size: 26px;
            line-height: 30px;
        }

        &__btn {
            min-width: 86px;
            padding: 12px 0 12px;
            font-size: 18px;
            line-height: 21px;

            &:nth-child(1) {
                margin-right: 40px;
            }
        }
    }
}
