.slider-mini-blocks {
    margin: rem(70) 0 rem(120);

    &__container {
        height: 100%;
    }

    &__slide {
        width: 100%;
        height: rem(167);
        background-size: cover;
        background-position: center center;
        position: relative;
        cursor: pointer;
    }

    &__controls {
        @extend .slider-inner__controls;
        min-height: 50px;
    }

    &__controls-container {
        @extend .slider-inner__controls-container;
        padding: 0 rem(55);
        margin-top: rem(17);
    }

    &__counter-current {
        @extend .slider-inner__counter-current;
    }

    &__counter-count {
        // @extend .slider-inner__counter-count;
        @extend .slider-inner__counter-current;
    }

    &__counter-separator {
        @extend .slider-inner__counter-separator;
    }

    &__arrow {
        @extend .slider-inner__arrow;

        &_left {
            @extend .slider-inner__arrow_left;
        }

        &_right {
            @extend .slider-inner__arrow_right;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slider-mini-blocks {
        &__arrow {
            svg {
                width: 25px;
                height: 11px;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .slider-mini-blocks {
        margin: 40px 0 40px;

        &__controls-container {
            padding: 0 rem(35);
        }

        &__arrow {
            svg {
                width: 19;
                height: 8px;
            }
        }
    }
}
