.product-next {
    position: fixed;
    bottom: rem(71);
    padding-left: rem(107);
    display: inline-block;
    opacity: 0;
    transition: opacity .3s ease;
    pointer-events: none;
    z-index: 2;

    &__icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        display: flex;
        justify-content: center;
        align-items: center;
        width: rem(76);

        img {
            position: relative;
            z-index: 1;
            // transform: translate3d(0, 25%, 0);
            transition: transform .3s ease;
        }
    }

    &__icon-circle {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        transform: scale(0);
        transition: transform .3s ease;

        &::before {
            @extend %pseudo;
            width: rem(76);
            height: rem(76);
            border-radius: 50%;
            background: $main-theme;
            top: 50%;
            left: 0;
            transform: translate3d(0, -50%, 0);
        }
    }

    &__text {
        display: block;
        transform: translate3d(-20%, 0, 0);
        transition: transform .3s ease;
    }

    &__label {
        line-height: 16px;
        color: $dark;
        margin-bottom: rem(10);
        display: block;
    }

    &__name {
        font-family: $font-secondary;
        font-size: rem(18);
        line-height: rem(22);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $dark;
        display: block;
    }

    &_visible {
        opacity: 1;
        pointer-events: all;

        .product-next {
            &__icon {
                img {
                    transform: none;
                }
            }

            &__icon-circle, &__label, &__name, &__text {
                transform: none;
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .product-next {
        bottom: 41px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .product-next {
        opacity: 1;
        pointer-events: all;
        position: absolute;
        bottom: 0;
        padding-left: 107px;

        &__icon-circle {
            &::before {
                width: 76px;
                height: 76px;
            }
        }

        &__icon-circle, &__label, &__name, &__text {
            transform: none;
        }
    }
}

@media screen and (max-width: $mobile) {
    .product-next {
        padding-left: 90px;
        position: relative;

        &__icon {
            width: rem(60px);

            img {
                transform: none;
                max-width: 30px;
            }
        }

        &__icon-circle {
            &::before {
                width: 66px;
                height: 66px;
            }
        }
    }
}
