.event {
    margin: rem(110) 0 rem(145);
    display: flex;
    justify-content: center;

    &__container {
        display: flex;
    }

    &__desc {
        width: rem(620);
        padding-right: rem(120);
    }

    &__img {
        width: rem(710);
    }

    &__title {
        font-family: $font-secondary;
        font-size: rem(48);
        line-height: rem(58);
        color: $dark;
        margin-bottom: rem(33);
    }

    &__text {
        @extend %text-size;
        line-height: 28px;
        color: $dark;
        margin-bottom: rem(27);
    }

    &__link {
        margin-top: rem(43);
        margin-bottom: 0;
    }
}

@media screen and (max-width: $laptop) {
    .event {
        &__desc {
            width: rem(500);
            padding-right: rem(58);
        }

        &__img {
            width: rem(544);
        }

        &__title {
            font-size: rem(32);
            line-height: rem(38);
            margin-bottom: rem(23);
        }

        &__text {
            margin-bottom: rem(17);
        }

        &__link {
            margin-top: rem(33);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .event {
        margin: 60px 0;

        &__container {
            width: 100%;
        }

        &__desc {
            padding-right: 31px;
            width: 100%;
        }

        &__img {
            min-width: rem(303);
        }

        &__text {
            display: none;
        }

        &__title {
            font-size: 28px;
            line-height: 34px;
            margin-bottom: 15px;
        }

        &__link {
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .event {
        margin: 40px 0;

        &__container {
            flex-wrap: wrap;
        }

        &__desc {
            width: 100%;
            padding-right: 0;
            margin-bottom: 25px;
        }

        &__title {
            font-size: 26px;
            line-height: 31px;
            margin-bottom: 10px;
        }

        &__img {
            min-width: 100%;
        }
    }
}
