.request {
    z-index: 10;
    position: fixed;
    right: 70px;
    bottom: 95px;
    border: 2px solid #40BEDA;
    background: #F8F5EF;
    // padding: 24px 15px 20px 15px;
    max-width: min-content;
    border-radius: 50%;
    cursor: pointer;
    transition: all .4s ease;

    &__block {
        width: 90px;
        height: 90px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__title {
        margin-bottom: 7px;

        &-text {
            color: $green;
            font-size: 16px;
            line-height: 18px;
            transition: all .4s ease;
            font-family: DINCondensed,sans-serif;
            line-height: 18px;
            letter-spacing: 0.1em;
        }
    }

    &__svg {
        // position: relative;
        // top: 0px;
        // transition: all .4s ease;

        svg {

            path {
                transition: all .4s ease;
            }
        }
    }


    &:hover {
        background: $green;
        transition: all .4s ease;
        bottom: 105px;

        .request {

            &__title {

                &-text {
                    color: #fff;
                    font-size: 16px;
                    line-height: 18px;
                    transition: all .4s ease;
                }
            }

            &__svg {
                // top: 5px;
                // transition: all .4s ease;

                svg {

                    path {
                        fill: #fff;
                        transition: all .4s ease;
                        $mobile: 767px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .request {
        position: fixed;
        right: 50px;
        bottom: 55px;
    }
}

@media screen and (max-width: $tablet-landscape) {
    .request {
        right: 10px;
        bottom: 50px;
        border: thin solid #40BEDA;

        &__block {
            width: 55px;
            height: 55px;
        }

        &__title {
            margin-bottom: 4px;

            &-text {
                font-size: 12px;
                line-height: 10px;
            }
        }

        &__svg {

            svg {
                width: 17px;
                height: 11.5px;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .request {
        right: 15px;
    }
}


div[data-widget="modal-form"] {
    position: absolute;
}
