.btn-menu {
    width: 48px;
    height: 36px;
    cursor: pointer;
    transform: translateZ(0);
    position: fixed;
    z-index: 997;
    right: 121px;
    top: 35px;
    align-items: center;
    display: none;;

    &__content {
        position: relative;
        width: 100%;
        height: 61%;
        overflow: hidden;
    }

    &__body {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    &__cross {
        position: absolute;
        // top: 50%;
        top: 10px;
        height: 2px;
        width: 100%;
        // left: 50%;
        // transform: translate3d(0, -50%, 0);
    }

    &__lines {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 6px 0;
        overflow: hidden;
    }

    &__line {
        width: 100%;
        height: 2px;
        background: #fff;
        transition: transform .3s ease;
        position: relative;

        &:last-child {
            margin-bottom: 0;
        }

        &_hamburger {
            margin-bottom: 5px;

            &:first-child {
                background: none;
                transition-delay: .5s;

                &::before, &::after {
                    @extend %pseudo;
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    transition: transform .3s ease;
                }

                &::before {
                    transform: translate3d(-18px, 0, 0);
                }

                &::after {
                    transform: translate3d(35px, 0, 0);
                }
            }

            &:nth-child(2) {
                transition-delay: .4s;
            }

            &:last-child {
                transition-delay: .3s;
            }
        }

        &_cross {
            position: absolute;
            top: 0;
        }

        &_cross-left {
            transform: translate3d(-50px, -15px, 0) rotate(15deg);
            transition-delay: .2s;
        }

        &_cross-right {
            transform: translate3d(50px, -15px, 0) rotate(-15deg);
            transition-delay: 0;
        }
    }

    &_open {
        .btn-menu {
            &__line {
                &_hamburger {
                    transform: translate3d(150%, 0, 0);

                    &:first-child {
                        transition-delay: 0s;

                        // &::before {
                        //     transform: translate3d(120%, 0, 0);
                        // }

                        // &::after {
                        //     transform: translate3d(120%, 0, 0);
                        // }
                    }

                    &:nth-child(2) {
                        transition-delay: .1s;
                    }

                    &:last-child {
                        transition-delay: .2s;
                    }
                }

                &_cross-left {
                    transform: rotate(15deg);
                    transition-delay: .3s;
                }

                &_cross-right {
                    transform: rotate(-15deg);
                    transition-delay: .4s;
                }
            }
        }
    }

    &_dark {
        .btn-menu {
            &__line {
                background: $dark;

                &_hamburger {
                    &:first-child {
                        background: none;

                        &::before, &::after {
                            background: $dark;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .btn-menu {
        display: flex;
        // position: absolute;
    }
}

@media screen and (max-width: $mobile) {
    .btn-menu {
        right: 20px;
        top: 19px;
        width: 34px;

        &__line {
            &_hamburger {
                &:first-child {
                    &::before {
                        transform: translate3d(-13px, 0, 0);
                    }

                    &::after {
                        transform: translate3d(25px, 0, 0);
                    }
                }
            }
        }
    }
}
