.copyright {
    position: fixed;
    z-index: 1000;
    bottom: rem(71);
    @extend %absolute-left;
    // color: $ui-color;

    &_dark {
        color: $dark;
        // color: $ui-color;
    }
}

@media screen and (max-width: $laptop) {
    .copyright {
        bottom: 41px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .copyright {
        position: absolute;
    }
}

@media screen and (max-width: $mobile) {
    .copyright {
        bottom: 20px;
    }
}
