.page-title {
    font-family: $font-secondary;
    line-height: rem(67);
    font-size: rem(56);
    letter-spacing: 0.1em;
    color: $dark;
    font-weight: normal;
    margin: rem(47) 0;
    text-transform: uppercase;

    &:first-child {
        margin-top: 0;
    }

    .title-offset__label + & {
        margin-top: 0;
    }
}

@media screen and (max-width: $laptop) {
    .page-title {
        line-height: rem(60);
        font-size: rem(50);
    }
}

@media screen and (max-width: $mobile) {
    .page-title {
        line-height: 48px;
        font-size: 40px;
        margin: 25px 0;

        &:first-child {
            margin-top: 0;
        }
    }
}
