.page-offset {
    padding-left: rem(102);

    &_short {
        max-width: rem(782);
    }
}

@media screen and (max-width: $laptop) {
    .page-offset {
        padding-left: rem(116);
    }
}

@media screen and (max-width: $tablet-portrait) {
    .page-offset {
        padding-left: 0;

        &_short {
            max-width: rem(590);
        }
    }
}
