.cite {
    margin: rem(130) 0;

    &__offset {
        @extend %col-offset;
    }

    &__container {
        display: flex;
        align-items: center;
        padding-left: 50px;
    }

    &__img {
        display: flex;
        flex: 0 0 367px;
        justify-content: flex-end;
    }

    &__img-wrapper {
        overflow: hidden;
        border-radius: 50%;
    }

    &__text {
        padding-left: rem(191);
    }

    &__text-quote {
        position: relative;
        max-width: 624px;
        @extend .text;

        &::before,
        &::after {
            @extend %pseudo;
            font-family: $font-secondary;
            font-size: 100px;
            line-height: 120px;
            color: $main-theme;
        }

        &::before {
            content: "";
            left: -77px;
            top: -60px;
        }

        &::after {
            content: "";
            right: -77px;
            top: 100%;
        }
    }
}

@media screen and (max-width: $laptop) {
    .cite {
        margin: rem(100) 0;

        &__container {
            padding-left: 25px;
        }

        &__img {
            flex: 0 0 300px;
        }

        &__text {
            padding-left: rem(157);
        }

        &__text-quote {
            max-width: 505px;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .cite {
        margin: 65px 0;

        &__container {
            padding-left: 0;
            align-items: flex-start;
        }

        &__img {
            flex: 0 0 180px;
        }

        &__text {
            padding-left: rem(67);
        }

        &__text-quote {
            max-width: 333px;

            &::before,
            &::after {
                font-size: 72px;
                line-height: 86px;
            }

            &::before {
                left: -45px;
                top: -30px;
            }
    
            &::after {
                right: -45px;
                top: 90%;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .cite {
        margin: 40px 0;

        &__container {
            flex-wrap: wrap;
        }

        &__img-wrapper {
            max-width: 120px;
        }

        &__img {
            flex: 0 0 100%;
            justify-content: center;
        }

        &__text {
            padding-left: 20px;
            padding-right: 20px;
        }

        &__text-quote {
            max-width: 333px;

            &::before,
            &::after {
                font-size: 36px;
                line-height: 43px;
            }

            &::before {
                left: -20px;
                top: -15px;
            }
    
            &::after {
                right: -20px;
                top: 95%;
            }
        }
    }
}
