.request-content {
    position: absolute;
    opacity: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);
    width: 92%;
    overflow: hidden;
    pointer-events: none;
    // height: 70%;
    background: #F1EDE5;
    box-shadow: 0px 0px 20px rgba(207, 203, 194, 0.2);
    z-index: 1100;
    transition: all .4s ease;

    &__close {
        border-radius: 50%;
        border: thin solid #E1D9C6;
        width: 58px;
        height: 58px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50px;
        right: 50px;
        cursor: pointer;

        svg {
            transition: all .4s ease;

            &:hover {
                transition: all .4s ease;
                transform: scale(1.1);
            }
        }
    }

    &__block {
        display: flex;

        &-img {
            flex: 0 0 38.61111rem;
            overflow: hidden;

            img {
                display: block;
                max-width: 110%;
                height: auto;
                margin: -1px;
            }
        }
    }

    &__title {
        line-height: rem(67);
        font-size: rem(56);
        color: #363636;
        margin-bottom: rem(50px);
    }

    &__form {
        overflow-y: scroll;
        padding: 4.5rem 8.27778rem 7.77778rem 7.77778rem;
        width: 100%;
        height: rem(680);

        &::-webkit-scrollbar {
            width: 0;
        }
    }

    &_active {
        pointer-events: all;
        opacity: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: fixed;
        transition: all .4s ease;

    }
}

@media screen and (max-width: $laptop) {

    .request-content {

        &__form {
            padding: 4.5rem 6.27778rem 7.77778rem 4.77778rem;
            height: rem(560);
        }

        &__block {

            &-img {
                flex-basis: 32.05556rem;
            }
        }

        &__title {
            line-height: rem(60);
            font-size: rem(50);
        }
    }
}

@media screen and (max-width: $tablet-landscape) {

    .request-content {

        &__form {
            padding: 2.5rem 7.27778rem 3.77778rem 4.77778rem;
            height: rem(520);
        }

        &__block {

            &-img {
                flex-basis: 30.05556rem;
            }
        }
        &__close {
            top: 25px;
            right: 25px;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .request-content {
            &__block {
                display: block;

                &-img {
                    display: none;
                }
            }

            &__form {
                width: 100%;
            }

            &__close {
                top: 15px;
                right: 15px;
            }
    }
}

@media screen and (max-width: $mobile) {
    .request-content {


        &__form {
            padding: 15px;
            // margin-top: 75px;
            height: rem(220);
        }

        &__close {
            width: 40px;
            height: 40px;

            svg {
                width: 13px;
                height: 13px;
            }
        }

        &__title {
            font-size: 26px;
            line-height: 31px;
            margin: 25px 0;
        }
    }
}


@media screen and (max-width: $mobile-small) {
    .request-content {
        width: 100%;
        height: 100%;

        &__form {
            padding: 15px;
            margin-top: 45px;
            height: 90vh;
            padding-bottom: 50px;
        }

        &__title {
            font-size: 26px;
            line-height: 31px;
            margin: 0 0 25px 0;
        }
    }
}
