.menu-mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 150px);
    background: $main-theme;
    // display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 150px;
    z-index: 995;
    display: none;
    opacity: 0;
    transition: opacity 1s ease;

    &__container {
        // padding-top: rem(280);
        padding-top: 100px;
        height: 100%;
        overflow: auto;
        // Remove the center alignment to make the overflow work correct
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    &__list {
        display: inline-block;
        padding-bottom: 100px;
        padding-top: 100px;
    }

    &__item {
        overflow: hidden;
        margin-bottom: rem(50);
        display: block;
        position: relative;
        text-align: center;
        transform: translate3d(0, 500%, 0);
        transition: transform .4s 0s cubic-bezier(.8,0,.14,1);
    }

    &__link {
        display: inline-block;
        font-family: $font-secondary;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        font-size: 28px;
        line-height: 34px;
        transform: translate3d(0, -200%, 0);
        transition: transform .4s 0s cubic-bezier(.8,0,.14,1);
    }

    .menu-dropdown__icon {
        opacity: 0;
        transition: opacity .3s ease;
    }

    &_open {
        display: block;

        .menu-mobile {
            &__container {
                overflow: hidden;
            }
        }
    }

    &_animated {
        transform: none;
        opacity: 1;

        .menu-mobile {
            &__container {
                overflow: auto;
            }

            &__item {
                transform: none;
                transition-duration: .4s;

                @for $i from 0 through 10 {
                    &:nth-child(#{$i}) {
                        .menu-mobile__link {
                            transition-delay: .1s * $i;
                        }
                    } 
                }
            }

            &__link {
                transform: none;
                transition-duration: .8s;
            }
        }

        .menu-dropdown__icon {
            opacity: 1;
            transition-delay: .5s;
        }
    }
}

@media screen and (max-width: $mobile) {
    .menu-mobile {
        &__container {
            padding-top: 75px;
        }

        &__list {
            padding-top: 50px;
            padding-bottom: 50px;
        }

        &__item {
            margin-bottom: rem(34);
        }

        &__link {
            font-size: 26px;
            line-height: 31px;
        }
    }
}
