.text {
    h2 {
        font-family: $font-secondary;
        line-height: rem(48);
        font-size: rem(38);
        letter-spacing: 0.1em;
        color: $dark;
        font-weight: normal;
        margin: rem(40) 0;
        text-transform: uppercase;

        &:first-child {
            margin-top: 0;
        }
    }

    h1 {
        @extend .page-title;
    }

    h4 + h2 {
        margin-top: rem(20);
    }

    h4 + h1 {
        margin-top: rem(15);
    }

    p {
        @extend %text-size;
        line-height: 36px;
        color: $dark;
        margin: rem(40) 0;

        + ul {
            margin-top: rem(-40);
        }
    }

    ul, ol {
        @extend %list-normalize;
        @extend %text-size;
        line-height: 36px;
        color: $dark;
    }

    ul {

        > li {
            margin-bottom: 10px;
            position: relative;
            padding-left: 38px;

            &::before {
                color: #E1D9C6;
                line-height: 18px;
                content:"\2022";
                font-size: 54px;
                font-weight: 700;
                position: absolute;
                top: 11px;
                left: 0;
            }
        }
    }

    ol {
        margin-left: 20px;
        list-style-type: decimal;

        li {
            margin-bottom: 10px;
        }
    }

    h4 {
        @extend .text-label;

        &:first-child {
            margin-top: 0;
        }

        & + p {
            margin-top: rem(10);
        }

        & + .text-icons {
            margin-top: rem(25);
        }

        & + .page-title {
            margin-top: 15px;
        }
    }

    .page-title {
        @extend .page-title;
    }

    a {
        @extend .link;
    }

    &_subtitle_short {
        h2 {
            max-width: rem(150);
        }
    }
}

@media screen and (max-width: $laptop) {
    .text {
        p {
            margin: rem(33) 0;

            + ul {
                margin-top: rem(-33);
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .text {
        h2 {
            margin: rem(33) 0;
            font-size: 42px;
            line-height: 60px;
        }

        h4 + h2 {
            margin-top: rem(20);
        }
    }
}

@media screen and (max-width: $mobile) {
    .text {
        h2 {
            line-height: rem(48);
            font-size: rem(40);
            margin: rem(25) 0;
        }

        h4 + h2 {
            margin-top: rem(10);
        }

        h4 {
            & + .text-icons {
                margin-top: 20px;
            }
        }

        p {
            line-height: 30px;
            margin: 25px 0;

            + ul {
                margin-top: -25px;
            }
        }

        a {
            &:last-child {
                margin-bottom: 0;
            }
        }

        &_subtitle_short {
            h2 {
                max-width: rem(200);
            }
        }
    }
}
