.slider-inner-mini {
    @extend .slider-inner;
    height: rem(700);

    &__container {
        @extend .slider-inner__container;
    }

    &__slide {
        @extend .slider-inner__slide;
        cursor: pointer;
    }

    &__slide-wrp {
        @extend .slider-inner__slide-wrp;
    }

    &__controls {
        @extend .slider-inner__controls;
    }

    &__controls-container {
        @extend .slider-inner__controls-container;
    }

    &__counter-current {
        @extend .slider-inner__counter-current;
    }

    &__counter-count {
        @extend .slider-inner__counter-count;
    }

    &__counter-separator {
        @extend .slider-inner__counter-separator;
    }

    &__arrow {
        @extend .slider-inner__arrow;

        &_left {
            @extend .slider-inner__arrow_left;
        }

        &_right {
            @extend .slider-inner__arrow_right;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slider-inner-mini {
        height: rem(500);
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slider-inner-mini {
        margin-bottom: 20px;
        height: rem(273);
    }
}

@media screen and (max-width: $mobile) {
    .slider-inner-mini {
        height: rem(148);
    
        &__counter-current {
            font-size: 18px;
            line-height: 22px;
        }

        &__arrow {
            top: 43%;
        }
    }
}