.accordion-list {
    &__item {
        @extend %text-size;
        line-height: 28px;
        color: $dark;
        padding-left: 40px;
        margin-bottom: 18px;
        position: relative;

        &:last-child {
            margin-bottom: 24px;
        }

        a {
            display: block;
        }
    }

    &__icon {
        position: absolute;
        left: 0;
        top: -2px;
        width: 21px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__line {
        display: block;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

@media screen and (max-width: $laptop) {
    .accordion-list {
        &__item {
            font-size: 14px;
            margin-bottom: 12px;

            &:last-child {
                margin-bottom: 17px;
            }
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .accordion-list {
        
    }
}

@media screen and (max-width: $mobile) {
    .accordion-list {
        &__item {
            padding-left: 0;
            font-size: 12px;
            line-height: 22px;
            margin-bottom: 10px;
        }

        &__icon {
            display: none;
        }
    }
}
