.development {
    position: fixed;
    z-index: 1000;
    bottom: rem(71);
    @extend %absolute-right;
    color: $ui-color;

    &_dark {
        // color: $dark;
        color: $ui-color;
    }
}

@media screen and (max-width: $laptop) {
    .development {
        bottom: 41px;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .development {
        position: absolute;
    }
}

@media screen and (max-width: $mobile) {
    .development {
        bottom: 20px;
    }
}
