.form-label {
    @extend %dots-overflow;
    display: block;
    margin-bottom: rem(10);
    font-weight: bold;
    position: relative;
    color: $gray;
    font-weight: normal;
}

@media screen and (max-width: $mobile) {
    .form-label {
        font-size: 12px;
        line-height: 14px;
        margin-bottom: 10px;
    }
}
