.products {

    &__container {
        display: flex;
        flex-wrap: wrap;
        margin: 0 rem(-85);
    }

    &__cell {
        width: (100% / 3);
        padding: 0 rem(85);
        margin-bottom: rem(75);
    }

    &__item {
        height: 100%;
        position: relative;

        &:hover {
            .products {
                &__item-bg {
                    transform: none;

                    .products {
                        &__item-name, &__item-name-icon {
                            transform: none;
                        }
                    }
                }
            }
        }

        &_animated {
            animation: product-filter-up 1s cubic-bezier(0.85, 0.03, 0.1, 0.99) forwards;
        }

        &_remove {
            animation: product-filter-down .2s cubic-bezier(0.85, 0.03, 0.1, 0.99) forwards;
            animation-delay: 0s!important;
        }
    }

    &__item-img {
        position: relative;
        display: flex;
        align-items: flex-end;
        // left: 0;
        width: 100%;
        padding: 0 rem(70);
        height: rem(560);
        margin-bottom: rem(42);
        z-index: 3;
        display: flex;
        justify-content: center;

        img {
            max-height: 100%;
            width: auto;
        }
    }

    &__item-name {
        text-align: center;
        // position: absolute;
        // top: calc(100% - #{rem(95)});
        // left: 0;
        width: 100%;
        color: #fff;
        transition: color .5s ease, transform .5s ease;
        z-index: 1;
        padding: 0 rem(60);

        &_dark {
            color: #000;
        }
    }

    &__item-name-text {
        font-family: $font-secondary;
        font-size: rem(30);
        line-height: rem(36px);
        letter-spacing: 0.1em;
    }

    &__item-name-mark {
        text-transform: lowercase;
        margin-top: 15px;
        margin-bottom: 40px;
        display: block;
        padding: 0 rem(30);
        font-size: 16px;
        line-height: 19px;
    }

    &__item-name-icon {
        position: absolute;
        right: rem(44);
        bottom: 46px;
        transition: opacity .3s .2s ease, transform .3s .2s ease;
        transform: translate3d(-50%, 0, 0);
    }

    &__item-bg-wrapper {
        position: absolute;
        left: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        // height: rem(500);
        overflow: hidden;
        z-index: 2;
    }

    &__item-bg {
        // @extend %pattern;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: transform .5s ease;
        transform: translate3d(-101%, 0, 0);
        overflow: hidden;

        &::before {
            @extend %pseudo;
            left: 0;
            top: 200px;
            width: 100%;
            height: 100%;
            background: $main-theme;
            z-index: -1;
        }

        .products__item-name {
            position: absolute;
            top: rem(560 + 42);
            left: 0;
            transform: translate3d(101%, 0, 0);
        }
    }

    &__item-link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 5;
    }

    &__message {
        width: 100%;
        height: 20vh;
        display: flex;
        color: $dark;
        @extend %text-size;
        justify-content: center;
        align-items: center;
    }
}

@keyframes product-filter-up {
    from {
        opacity: 0;
        transform: translate3d(0, 25%, 0);
    }

    to {
        opacity: 1;
        transform: none;
    }
}

@keyframes product-filter-down {
    from {
        opacity: 1;
        // transform: none;
    }

    to {
        opacity: 0;
        // transform: translate3d(0, 25%, 0);
    }
}

@media screen and (max-width: $laptop) {
    .products {
        &__cell {
            padding: 0 rem(15);
            margin-bottom: rem(45);
        }

        &__container {
            margin: 0 rem(-15);
        }

        &__item-bg {
            .products__item-name {
                top: rem(485 + 40);
            }
        }

        &__item-bg-wrapper {
            // height: rem(482);
        }

        &__item-img {
            height: rem(485);
            margin-bottom: rem(40);
        }

        &__item-name {
            font-size: rem(32);
            line-height: rem(38);
        }

        &__item-name-icon {
            right: rem(33);
            top: rem(5);
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .products {
        &__item {
            &:hover {
                .products {
                    &__item-bg {
                        transform: translate3d(-101%, 0, 0);
    
                        .products {
                            &__item-name {
                                transform: translate3d(101%, 0, 0);
                            }

                            &__item-name-icon {
                                transform: translate3d(-50%, 0, 0);
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .products {
        &__item-bg-wrapper {
            // height: rem(350);
        }

        &__item-bg {
            .products__item-name {
                top: rem(284 + 13);
            }
        }

        &__item-img {
            padding: 0 rem(35);
            height: rem(284);
            margin-bottom: rem(13);
        }

        &__item-name {
            padding: 0 rem(20);
            font-size: 28px;
            line-height: 34px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .products {
        &__cell {
            width: 100%;
            margin-bottom: rem(45);

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__item-bg {
            .products__item-name {
                top: rem(284 + 25);
            }
        }

        &__item-img {
            padding: 0 rem(89);
            margin-bottom: rem(25);
        }

        &__item-name {
            font-size: rem(26);
            line-height: rem(31);
            position: relative;
            top: 0;
        }

        &__item-bg-wrapper {
            height: auto;
        }
    }
}
