.product-layout {
    height: 100%;
    background: $skinny;
    display: flex;

    &__info {
        position: relative;
        background: $main-theme;
        height: 100%;
        min-width: 35%;

        &.no-spot {
            canvas {
                display: none;
            }
        }
    }

    &__info-back {
        display: none;
        z-index: 2;
    }

    &__info-content {
        height: 100%;
        display: flex;
        margin-left: rem(160);
        align-items: center;
        position: relative;
        z-index: 2;
    }

    &__info-item {
        margin-bottom: rem(92);

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__content {
        height: 100%;
        width: 75%;
        position: relative;

        &.no-spot {
            canvas {
                display: none;
            }
        }
    }

    &__content-bottle {
        position: absolute;
        width: rem(300);
        margin-right: rem(-150);
        top: 0;
        right: 0;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 2;

        img {
            max-height: rem(750);
        }
    }

    &__content-wrp {
        position: absolute;
        top: rem(158);
        right: 0;
        width: rem(998);
        z-index: 2;
        @extend %padding-right;
    }

    &__content-back {
        display: flex;
        justify-content: flex-end;
        margin-bottom: rem(70);
    }

    &__content-scroll {
        width: rem(685);
        height: rem(540);
        position: relative;
    }

    &__content-next {
        margin-top: rem(65);
    }

    &_link {
        &_muted {
            margin-top: 10px;
            margin-bottom: 0;
        }

        &_default {
            margin-top: 45px;
        }
    }
}

@media screen and (max-width: $laptop) {
    .product-layout {
        &__info {
            min-width: rem(495);
        }

        &__info-content {
            margin-left: rem(100);
        }

        &__content-bottle {
            width: rem(200);
            margin-right: rem(-100);
            z-index: 2;
    
            img {
                max-height: rem(659);
            }
        }

        &__content-wrp {
            top: rem(127);
            width: rem(768);
        }

        &__content-back {
            margin-bottom: rem(63);
        }

        &__content-scroll {
            width: rem(625);
            height: 60vh;
        }

        &__content-next {
            margin-top: rem(35);
        }

        &_link {

            &_default {
                margin-top: 40px;
            }
        }
    }
}

@media screen and (max-height: $notebook-height) {
    .product-layout {

        &__content-wrp {
            top: rem(110);
        }

        &__content-back {
            margin-bottom: rem(40);
        }

        &__content-scroll {
            height: 45vh;
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .product-layout {
        &__content-next {
            margin-top: rem(60);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .product-layout {
        flex-wrap: wrap;

        &__info {
            width: 100%;
            height: rem(676);
        }

        &__info-content {
            margin-left: rem(60);
        }

        &__info-list {
            margin-top: rem(80);
        }

        &__info-item {
            margin-bottom: rem(39);
        }

        &__content-bottle {
            right: 50%;
            width: rem(170);
            margin-right: rem(-85);
            z-index: 2;
    
            img {
                max-height: rem(531);
            }
        }

        &__content {
            width: 100%;
        }

        &__content-back {
            display: none;
        }

        &__info-back {
            position: absolute;
            display: block;
            top: rem(141);
            right: rem(60);
        }

        &__content-wrp {
            position: relative;
            top: 0;
            width: 100%;
            padding: 55px 60px;
        }

        &__content-scroll {
            width: 100%;
            height: auto;
        }

        &__content-next {
            margin-top: rem(40);
            margin-bottom: rem(70);
        }
    }
}

@media screen and (max-width: $mobile) {
    .product-layout {
        &__info {
            height: rem(454);
            min-width: 100%;
        }

        &__info-content {
            margin-left: 44px;
            display: block;
        }

        &__info-list {
            margin-top: 80px;
        }

        &__info-back {
            display: none;
        }

        &__content-bottle {
            right: rem(44);
            margin-right: 0;
            top: 80px;
            width: rem(111);
            align-items: flex-start;
            z-index: 2;
    
            img {
                max-height: rem(346);
            }
        }

        &__info-item {
            margin-bottom: rem(30);
        }

        &__content-wrp {
            padding: 45px 30px 0 30px;
        }

        &__content-next {
            margin-top: rem(70);
            margin-bottom: rem(100);
        }

        &_link {
    
            &_default {
                margin-top: 30px;
            }
        }
    }
}
