.catalog {

    &__grid {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: (100% / 3);
        position: relative;

        &:hover {
            z-index: 1;

            .catalog {
                &__item-img {
                    transform: scale(1.05);
                    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.3);
                }
            }
        }
    }

    &__item-img {
        background-size: cover;
        background-position: center center;
        width: 100%;
        height: rem(671);
        transition: transform .3s ease, box-shadow .3s ease;
    }

    &__desc {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0 rem(60) rem(60);
    }

    &__text {
        font-family: $font-secondary;
        font-size: rem(26);
        line-height: rem(31);
        text-transform: uppercase;
        max-width: rem(265);
        letter-spacing: 0.1em;
    }

    &__link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: $laptop) {
    .catalog {
        &__item-img {
            height: rem(502);
        }

        &__text {
            font-size: rem(22);
            line-height: rem(26);
            max-width: rem(225);
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .catalog {
        &__item {
            &:hover {
                .catalog {
                    &__item-img {
                        transform: none;
                        box-shadow: none;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .catalog {
        &__item {
            width: (100% / 2);
        }

        &__item-img {
            height: rem(369);
        }

        &__desc {
            padding: 0 rem(30) rem(30);
        }

        &__text {
            max-width: rem(160);
        }
    }
}

@media screen and (max-width: $mobile) {
    .catalog {
        &__item {
            width: 100%;
        }

        &__item-img {
            height: rem(320);
        }

        &__text {
            font-size: rem(18);
            line-height: rem(22);
            max-width: rem(140);
        }
    }
}
