.src-navigation {
    position: fixed;
    top: 0;
    right: 0;
    width: 0;
    // left: calc(100% - 83px);
    height: var(--app-height);
    z-index: 1005;

    &__bulges {
        height: 100%;
        position: absolute;
        right: 0;
        z-index: 5;
    }

    &__buldge {
        display: flex;
        align-items: center;
        height: 100%;
        position: absolute;
        right: 0;
        transition: transform .3s 1s ease;

        &_light {
            transform: translate3d(200%, 0, 0);
            transition-delay: 0s;
        }
    }

    &__content {
        width: 100vw;
        height: 100vh;
        left: 0;
        position: relative;
        z-index: 4;
        overflow: auto;
        overflow-x: hidden;
        background-color: #363636;
        transition: transform .7s 0s ease;
        will-change: transform;
    }

    &__shade {
        position: relative;
        left: 0;
        width: 83px;
        height: 880px;
        overflow: hidden;
        cursor: pointer;
    }

    &__light {
        position: relative;
        left: 0;
        width: 83px;
        height: 880px;

        .src-navigation {
            &__svg {
                path {
                    fill: white;
                }
            }
        }
    }

    &__bg {
        position: absolute;
        right: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        pointer-events: none;

        svg {
            width: 100%;
            height: 100%;
        }
    }

    &__overlay {
        width: 100vw;
        // height: 100%;
        // background-color: #363636;
        font-size: .833335vw; // 16
        padding: em(75, 16) 0 em(75, 16) em(100, 16);

        > div {
            padding-right: em(100, 16);
            // max-height: calc(var(--app-height) - 9.375em);
            // height: 100%;
            // overflow: auto;
            // overflow-x: hidden;
        }
    }

    &__link {
        display: block;
        width: 100%;
        height: 100%;
    }

    &__svg {
        width: 100%;
        height: 100%;

        path {
            fill: #363636;
        }
    }

    &__letter {
        display: block;
        position: absolute;
        font-size: rem(30);
        line-height: rem(40);
        left: 50%;
        top: 50%;
        transform: translate3d(0, -50%, 0);

        svg {
            fill: #fff;
            width: 24px;
            height: 23px;
        }
    }

    &__close {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%);
        width: em(24, 16);
        height: em(24, 16);
        cursor: pointer;

        // &:hover {
        //     rect {
        //         fill: #c2c2c2;
        //     }
        // }

        rect {
            fill: #363636;
            transition: fill .2s ease;
            will-change: fill;
        }
    }

    &--active {
        z-index: 999999;

        .src-navigation {
            &__buldge {
                &_dark {
                    transition-delay: 0s;
                    transform: translate3d(100%, 0, 0);
                }

                &_light {
                    transition-delay: .4s;
                    transform: none;
                }
            }

            &__content {
                transform: translateX(-100%);
                transition-delay: .7s;
            }
        }

        .src-navigation-grid {
            &__col {
                transition: transform 1s ease, opacity 1.8s ease;
                opacity: 1;
                transform: none;

                @for $i from 0 through 3 {
                    &:nth-child(#{$i+1}) {
                        transition-delay: .3s + .1s * $i;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .src-navigation {
        // left: calc(100% - 52px);

        &__shade, &__light {
            width: 52px;
            height: 555px;
        }

        &__overlay {
            font-size: .97222vw; // 14
            padding: 7vh 0 0 em(75, 14);

            > div {
                padding-right: em(75, 14);
                max-height: calc(var(--app-height) - 18.5vh);
            }
        }

        &__close {
            right: em(30, 14);
            width: em(22, 14);
            height: em(22, 14);
        }

        &__letter {
            svg {
                width: 17px;
                height: 16px;
            }
        }

        &--active {
            .src-navigation {
                &__content {
                    transform: translateX(-100%);
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .src-navigation {
        display: none;
    }
}
