.category {
    height: 100%;
    position: relative;
    z-index: 2;

    &__container {
        position: relative;
        height: 100%;
        margin: 0 rem(225) 0;
        padding-top: rem(35);
    }

    &__content {
        height: 100%;
        padding-top: rem(80);
    }

    &__controls {
        margin-bottom: rem(120);
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        left: 0;
        right: 0;
    }

    &__form {
        display: flex;
        flex-wrap: wrap;
    }

    &__control {
        margin-right: 30px;
        margin-bottom: 15px;
    }
}

@keyframes swing {
    from { transform: none; }
    50% { transform: translate3d(0, -50%, 0) }
    to { transform: none; }
}

@media screen and (max-width: $laptop) {
    .category {
        &__container {
            margin: 0 rem(100);
            padding-top: rem(30);
        }

        &__controls {
            .back {
                display: none;
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .category {
        &__container {
            margin: 0 rem(60);
        }
    }
}

@media screen and (max-width: $mobile) {
    .category {
        &__container {
            margin: 0 20px;
            padding-top: 20px;
        }

        &__content {
            padding-top: rem(35);
        }

        &__controls {
            z-index: 5;
            position: relative;
            margin-bottom: 0;
            // right: -20px;
            // left: -20px;
            // overflow: auto;

            .buttons-radio {
                padding-right: 20px;
                padding-left: 20px;
            }
        }

        &__control {
            width: 100%;
            margin-right: 0;
        }
    }
}
