.modal {
    position: fixed;
    background: rgba(0, 0, 0, 1);
    z-index: 10000;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity .3s ease;

    &__body {
        width: 100%;
        height: 100%;
        padding: 75px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__container {
        width: 100%;
        height: 100%;

        iframe {
            width: 100%!important;
            height: 100%!important;
        }
    }

    &__close {
        position: absolute;
        width: 32px;
        height: 32px;
        top: 20px;
        right: 20px;
        transform: rotate(45deg);
        cursor: pointer;

        &::before,
        &::after {
            @extend %pseudo;
            background: #fff;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%, -50%, 0);
        }

        &::before {
            height: 4px;
            width: 100%;
        }

        &::after {
            width: 4px;
            height: 100%;
        }
    }

    &_active {
        display: block;
    }

    &_animated {
        opacity: 1;
    }
}

@media screen and (max-width: $mobile) {
    .modal {

        &__body {
            padding-left: 20px;
            padding-right: 20px;
            padding-bottom: 20px;
        }
    }
}
