.list-icon {
    margin: rem(47) 0;

    &__item {
        position: relative;
        margin-bottom: rem(22);
        padding-left: rem(88);
        min-height: rem(60);
        display: flex;
        align-items: center;
        max-width: rem(540);

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__icon {
        position: absolute;
        left: 0;
        top: 50%;
        width: rem(58);
        height: rem(58);
        border-radius: 50%;
        border: 2px solid #E1D9C6;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translate3d(0, -50%, 0);
    }

    &__text {
        @extend %text-size;
        line-height: rem(30);
        color: $dark;
    }
}

@media screen and (max-width: $laptop) {
    .list-icon {
        
    }
}

@media screen and (max-width: $tablet-portrait) {
    .list-icon {
        margin: 43px 0;
    }
}

@media screen and (max-width: $mobile) {
    .list-icon {
        &__item {
            padding-left: 0;
            padding-top: 46px;
            min-height: 0;
            margin-bottom: 20px;
        }

        &__icon {
            width: 36px;
            height: 36px;
            top: 0;
            transform: none;

            svg {
                width: 20px;
                height: 20px;
            }
        }

        &__text {
            line-height: 26px;
        }
    }
}
