.grid-quad {
    margin: rem(77) 0 rem(130);

    &__container {
        display: flex;
        margin: -20px;
    }

    &__item {
        padding: 20px;
        width: (100% / 4);
    }

    &__item-body {
        position: relative;
        
        &:hover {
            .card-content__more-text::before {
                transform: translate3d(5px, -50%, 0);
            }
        }
    }

    &__img {
        img {
            display: block;
        }
    }

    &__bg {
        display: none;
        // @extend %pattern-blue;
        background-color: $main-theme;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: opacity .3s ease;
    }

    &__link {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &_type {
        &_rect {
            .grid-quad {
                &__item {
                    width: (100% / 2);
                }

                &__item-body {
                    &:hover {
                        .grid-quad {
                            &__bg {
                                opacity: 1;
                            }
                        }
                    }
                }

                &__img {
                    // height: rem(409);
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .grid-quad {
        margin: rem(60) 0;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .grid-quad {

        &__container {
            margin: -17px;
            flex-wrap: wrap;
        }

        &__item {
            padding: 17px;
            width: (100% / 2);
        }

        &_type {
            &_rect {
                .grid-quad {
                    &__item {
                        width: 100%;
                    }
    
                    &__img {
                        height: rem(315);
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .grid-quad {
        margin: 25px 0;

        &__container {
            margin: -12px;
        }

        &__item {
            padding: 12px;
            width: 100%;
        }
    }
}
