.preloader {
    position: fixed;
    z-index: 2000;
    background: $main-theme;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: transform 2s cubic-bezier(.8,0,.14,1);
    overflow: hidden;

    &__logo {
        position: absolute;
        z-index: 10;
        width: 112px;
        height: 140px;
        top: rem(117);
        left: 50%;
        transform: translate3d(-50%, 0, 0);

        svg {
            transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
            fill: #fff;
        }
    }

    &__logo-inner {
        overflow: hidden;
        transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);

        svg {
            transition: transform 1.5s .2s cubic-bezier(.8,0,.14,1);
            fill: #fff;
        }
    }

    &__content {
        width: 100%;
        height: 100%;
        background: $main-theme;
        overflow: hidden;
    }

    &__gradient {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(50% 50% at 50% 50%, rgba(35, 174, 163, 0) 0%, rgba(35, 174, 163, 0.2625) 26.56%, rgba(35, 174, 163, 0.598437) 63.02%, rgba(35, 174, 163, 0.8) 100%);
        display: none;
    }

    &_done {
        transform: translate3d(0, -100%, 0);

        .preloader {
            &__logo-inner {
                transform: translate3d(0, 500%, 0);

                svg {
                    transform: translate3d(0, 200%, 0);
                }
            }
        }
    }

    .no-spot {
        .preloader {
            &__gradient {
                display: block;
            }
        }
    }
}

@media screen and (max-width: $notebook) {
    .preloader {
        &__logo {
            top: rem(70);
            width: 70px;
            height: 95px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .preloader {
        &__logo {
            top: 74px;
            width: 53px;
            height: 65px;
        }
    }
}
