.src-navigation-grid {
    &__inner {
        display: flex;
        flex-wrap: wrap;
        margin: 0 em(-40, 16);
    }

    &__col {
        position: relative;
        width: (100% / 4);
        padding: 0 em(40, 16);
        transition: transform 1s ease, opacity .2s ease;
        opacity: 0;
        transform: translate3d(150%, 0, 0);
        will-change: transform, opacity;

        @for $i from 0 through 2 {
            &:nth-child(#{$i+1}) {
                transition-delay: .05s * (3 - $i);
            }
        }
    }
}

