.slider-scroll {
    height: 100%;

    &_desktop {
        .slider-scroll__wrapper {
            transition-duration: .4s!important;
            transition-timing-function: cubic-bezier(0.2, 0.43, 0.24, 0.96)!important;
        }
    }

    &_spaced {
        .slider-scroll__slide {
            padding-bottom: rem(156);
        }
    }

    &_page {
        padding: rem(154) 0 rem(140);
    }

    &_touch.slider-scroll_desktop {
        .slider-scroll__wrapper {
            transition-duration: 0s!important;
        }
    }

    &__container {
        height: 100%;

        &.overflow {
            overflow: visible;
        }
    }

    &__slide {
        height: auto;
    }
}

@media screen and (max-width: $laptop) {
    .slider-scroll {

        &_page {
            padding: rem(100) 0;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slider-scroll {
        &__slide {
            padding-bottom: 100px;
        }

        &_spaced {
            .slider-scroll__slide {
                padding-bottom: 100px;
            }
        }

        &_page {
            padding: 127px 0 0;
        }

        // > .swiper-container {
        //     overflow: visible;
        // }
    }
}

@media screen and (max-width: $mobile) {
    .slider-scroll {
        &__slide {
            padding-bottom: 100px;
        }

        &_spaced {
            .slider-scroll__slide {
                padding-bottom: 100px;
            }
        }

        &_page {
            padding: 89px 0 0;
        }
    }
}
