.sellers-grid {
    margin-right: rem(113px);
    margin-top: 60px;
    min-height: 195px;

    &__container {
        margin: 0 -15px;
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: (100% / 6);
        padding: 0 15px;
        margin-bottom: 30px;
    }

    &__link {
        display: block;
        height: rem(73);
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid #E1D9C6;
        padding: 10px;
        color: #8A8885;
        transition: color .3s ease;

        img {
            max-width: 100%;
            height: auto;
            filter: grayscale(100%);
            transition: filter .3s ease;
        }

        &:hover {
            color: $dark;

            img {
                filter: grayscale(0%);
            }
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .sellers-grid {
        margin-right: 0;

        &__container {
            margin: 0 -20px;
        }

        &__item {
            width: (100% / 3);
            padding: 0 20px
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .sellers-grid {
        max-width: rem(590);
    }
}

@media screen and (max-width: $mobile) {
    .sellers-grid {
        margin-top: 40px;

        &__container {
            margin: 0 -5px;
        }

        &__item {
            width: (100% / 2);
            padding: 0 5px
        }
    }
}
