.buttons-radio {
    &__container {
        display: flex;
    }

    &__item {
        @extend %text-size;
        position: relative;
        margin-right: rem(20);
        padding: rem(12) rem(40) rem(13);
        border: 2px solid #E1D9C6;
        color: #333333;
        background: $skinny;
        line-height: rem(19);
        overflow: hidden;
        transition: background .3s ease;
        cursor: pointer;

        &:last-child {
            margin-right: 0;
        }

        &::before {
            @extend %pseudo;
            left: 0;
            top: 0;
            width: 110%;
            height: 100%;
            background: #E1D9C6;
            transition: transform .5s cubic-bezier(1, 0, 0, 1);
            transform-origin: 0 0;
            transform: scaleY(0);
        }

        &_active, &:hover {
            &::before {
                transform-origin: 0 100%;
                transform: scaleY(1);
            }
        }
    }

    &__text {
        position: relative;
        z-index: 2;
    }
}

@media screen and (max-width: $laptop) {
    .buttons-radio {
        &__item {
            padding: rem(12) rem(30) rem(13);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .buttons-radio {
        &__item {
            font-size: 14px;
            line-height: 16px;
            padding: rem(8) rem(25) rem(9);
            margin-right: rem(15);
        }
    }
}

@media screen and (max-width: $mobile) {
    .buttons-radio {

    }
}
