.logo {
    user-select: none;
    display: block;
    width: rem(50);
    height: rem(63);
    position: fixed;
    top: rem(52);
    @extend %absolute-left;
    z-index: 1000;

    svg {
        fill: #fff;
        // fill: $ui-color;
        transition: fill .5s ease;
    }

    &_dark {
        svg {
            fill: $dark;
            // fill: $ui-color;
        }
    }
}

@media screen and (max-width: $laptop) {
    .logo {
        top: 20px;
        width: 50px;
        height: 63px;
    }
}

@media screen and (max-width: $tablet-landscape) {
    .logo {
        
    }
}

@media screen and (max-width: $tablet-portrait) {
    .logo {
        top: 30px;
        // position: absolute;
    }
}

@media screen and (max-width: $mobile) {
    .logo {
        top: 20px;
        width: 27px;
        height: 34px;
    }
}
