.content-limiter {

    ul {

        > li {
            margin-bottom: 10px;
            position: relative;
            padding-left: 38px;

            &::before {
                color: #E1D9C6;
                line-height: 18px;
                content:"\2022";
                font-size: 54px;
                font-weight: 700;
                position: absolute;
                top: 11px;
                left: 0;
            }
        }
    }
    ol {
        margin-left: 20px;
       list-style-type: decimal;

        li {
            margin-bottom: 10px;
        }
    }


}

@media screen and (min-width: $tablet-portrait + 1) {
    .content-limiter {
        &__body {
            min-height: 0;
            max-height: 100%;
        }

        &__controls {
            display: none;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .content-limiter {
        &__controls {
            padding: 30px 0 0;
        }

        &__body {
            min-height: 145px;
            max-height: 0;
            overflow: hidden;
            transition: max-height .3s ease;

            p {
                &:first-child {
                    margin-top: 0;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &__btn {
            position: relative;
            padding-right: 21px;
            display: inline-block;
            cursor: pointer;
        }

        &__btn-text {
            font-size: 16px;
            line-height: 19px;
            color: $dark;
        }

        &__btn-icon {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate3d(0, -50%, 0);
        }

        &_active {
            .content-limiter {
                &__btn-icon {

                    svg {
                        transform: rotate(-180deg);
                    }
                }
            }
        }

        &_disabled {
            .content-limiter {
                &__body {
                    min-height: 0;
                    max-height: 100%;
                }

                &__controls {
                    display: none;
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .content-limiter {

        &__controls {
            padding: 25px 0 0;
        }

        ul {

            > li {
                line-height: 28px;
                margin-bottom: 5px;

                &::before {
                    color: #E1D9C6;
                    line-height: 18px;
                    content:"\2022";
                    font-size: 54px;
                    font-weight: 700;
                    position: absolute;
                    top: 8px;
                    left: 0;
                }
            }
        }
    }
}
