.src-navigation-item {
    &__inner {
        color: #fff;

        > a {
            display: block;

            &:hover {
                .src-navigation-item {
                    &__btn {
                        color: #c2c2c2;

                        > svg {
                            transform: translateX(em(5, 16));

                            path { fill: #c2c2c2; }
                        }
                    }
                }
            }
        }
    }

    &__icon {
        margin-bottom: 2.1vh;

        > svg {
            width: em(50, 16); height: em(50, 16);
        }
    }

    &__title {
        text-transform: uppercase;
        margin-bottom: em(24, 48);
        font-size: em(48, 16);
        line-height: 58px;
        font-family: $font-secondary;
    }

    &__description {
        font-weight: 500;
        color: #c2c2c2;
        line-height: 1.6;
    }

    &__btn {
        margin-top: 3vh;
        display: inline-block;
        color: #6a6a6a;
        line-height: 1.25;
        display: flex;
        align-items: center;
        transition: color .2s ease;
        will-change: color;

        > svg {
            margin-left: em(20, 16);
            width: em(24, 16); height: em(24, 16);
            transition: transform .2s ease;
            will-change: transform;

            path {
                transition: fill .2s ease;
                will-change: fill;
            }
        }
    }

    &__links {
        position: absolute;
        top: 100%;
        left: em(40, 16);
        width: calc(100% - 5em);
        margin-top: 7.5vh;
        padding-bottom: 7.5vh;

        &-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 4vh;

            &:hover {
                .src-navigation-item {
                    &__links {
                        &-btn { color: #c2c2c2; }
                        &-icon {
                            svg {
                                path { fill: #c2c2c2; }
                            }
                        }
                    }
                }
            }

            &:last-child { margin: 0; }

            &--active {
                pointer-events: none;

                .src-navigation-item {
                    &__links {
                        &-label { color: #c2c2c2; }

                        &-btn, &-icon { display: none; }
                    }
                }
            }

            &--disabled {
                pointer-events: none;

                .src-navigation-item {
                    &__links {
                        &-btn, &-icon { display: none; }
                    }
                }
            }
        }

        &-label {
            font-size: em(18, 16);
            line-height: 1.6;
            max-width: 61.5%;
            font-family: $font-secondary;
            letter-spacing: .01em;
            text-transform: uppercase;
        }

        &-btn {
            font-size: em(14, 16);
            line-height: 1.7;
            color: #6a6a6a;
            transition: color .2s ease;
            will-change: color;
        }

        &-icon {
            svg {
                margin-left: em(20, 16);
                width: em(24, 16); height: em(24, 16);
                transition: transform .2s ease;
                will-change: transform;

                path {
                    transition: fill .2s ease;
                    will-change: fill;
                }
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .src-navigation-item {
        &__icon {
            margin-bottom: 0;

            > svg {
                width: em(45, 14); height: em(45, 14);
            }
        }

        &__title {
            font-size: em(36, 14);
            margin-bottom: 0;
        }

        &__description {
            font-size: 14px;
            line-height: 1.6;
        }

        &__btn {
            font-size: em(20, 16);
            margin-top: 2.6vh;
        }

        &__link {
            &:hover {
                > svg { transform: translateX(em(5, 14)); }
            }

            > svg {
                margin-left: em(15, 14);
                width: em(20, 14); height: em(20, 14);
            }
        }

        &__links {
            left: em(30, 14);
            width: calc(100% - 4.28572em);
            margin-top: 50px;
            // padding-bottom: 0;

            &-row {
                margin-bottom: 3vh;
            }

            &-label {
                font-size: em(20, 14);
            }

            &-link { font-size: em(14, 14); }
        }
    }
}
