$at_font-color: #363636;
$at_bg-color: #F8F5EF;
$at_active-color: #F0EDE5;
$at_thumb: #E1D9C6;
$at_scroll-bg: #F8F5EF;

.autocomplete {
    position: relative;

    &__list {
        position: absolute;
        top: calc(100% + 10px);
        left: 0;
        right: 0;
        max-height: 195px;
        padding: 10px 0;
        border-radius: 5px;
        overflow: auto;
        background: $at_bg-color;
        scrollbar-base-color: $at_scroll-bg;
        scrollbar-face-color: $at_thumb;
        scrollbar-shadow-color: $at_thumb;
        scrollbar-color: $at_thumb $at_scroll-bg;
        scrollbar-width: thin;
        opacity: 0;
        pointer-events: none;
        transition: opacity .5s ease;

        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-track-piece {
            background-color: $at_scroll-bg;
        }

        &::-webkit-scrollbar-thumb {
            background-color: $at_thumb;
        }
    }

    &__item {
        padding: 10px 21px;
        font-size: 16px;
        line-height: 19px;
        color: $at_font-color;
        cursor: pointer;

        &:hover, &_active {
            background: $at_active-color;
        }

        span {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    &_active {
        .autocomplete {
            &__list {
                opacity: 1;
                pointer-events: all;
            }
        }
    }
}
