.slide-title {
    position: relative;
    padding-right: rem(160);
    margin-right: rem(-36);

    &__text {
        font-family: $font-secondary;
        font-size: rem(82);
        text-align: right;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        line-height: rem(98);
        font-weight: normal;
    }

    &__icon {
        position: absolute;
        bottom: rem(15);
        right: 0;
        @extend %circle;

        &_video {
            transition: opacity .3s ease;
            cursor: pointer;

            &:hover {
                .slide-title {
                    &__icon-inner {
                        transform: scale(1.1);

                        svg {
                            // animation: swing-right 2s ease infinite;
                        }
                    }

                    &__icon-wrp {
                        transform: scale(.9);
                    }
                }
            }

            .slide-title {
                &__icon-inner {
                    padding-bottom: 0;
                    padding-left: rem(3);
                }
            }
        }

        &_arrow {
            cursor: pointer;

            .slide-title {
                &__icon-wrp {
                    padding-left: 10px;
                    padding-top: 5px;
                }
            }
        }
    }

    &__icon-inner {
        width: 100%;
        height: 100%;
        background: #fff;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: rem(5);
        transition: transform .3s ease;

        svg {
            transition: transform .5s ease;
        }
    }

    &__icon-wrp {
        transition: transform .5s ease;
    }

    &__text-line {
        display: block;
    }

    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: $laptop) {
    .slide-title {
        padding-right: rem(120);
        margin-right: rem(-30);

        &__text {
            font-size: rem(60);
            line-height: rem(72);
        }

        &__icon {
            bottom: rem(7);

            &_arrow {

                .slide-title {
                    &__icon-wrp {

                        svg {
                            width: 12px;
                            height: 25px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .slide-title {
        &__icon {
            bottom: rem(-7);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slide-title {
        padding-right: 0;
        margin-right: 0;
        padding-bottom: rem(112);

        &__text {
            text-align: center;
            font-size: rem(60);
            line-height: rem(72);
        }

        &__icon {
            bottom: 0;
            right: 50%;
            margin-right: -34px;

            &_arrow {
                .slide-title {
                    &__icon-wrp {
                        padding-left: 8px;
                        padding-top: 7px;

                        svg {
                            width: 11px;
                            height: 23px;
                        }
                    }
                }
            }
        }

        &__icon-inner {
            padding-bottom: rem(3);
        }
    }
}

@media screen and (max-width: $mobile) {
    .slide-title {
        padding-bottom: rem(76);

        &__text {
            font-size: rem(40);
            line-height: rem(48);
        }

        &__icon {
            margin-right: -26px;
            bottom: 15px;

            svg {
                width: 15px;
                height: 6px;
            }

            &_arrow {
                .slide-title {
                    &__icon-wrp {
                        padding-left: 6px;
                        padding-top: 3px;

                        svg {
                            width: 9px;
                            height: 19px;
                        }
                    }
                }
            }

            &_video {

                svg {
                    width: 14px;
                    height: 15px;
                }

                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}
