.select {
    position: relative;
    border: 2px solid $gold;
    width: 244px;
    background: $skinny-light;

    &_open {
        .select {
            &__list {
                opacity: 1;
                pointer-events: all;
            }

            &__list-inner {
                transform: none;
            }

            &__arrow {
                > svg {
                    transform: none;
                }
            }
        }
    }

    &__arrow {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        background: none;
        border: none;
        outline: none;
        cursor: pointer;

        svg {
            transform: rotate(180deg);
            transition: transform .3s ease;
        }
    }

    &__list {
        position: absolute;
        pointer-events: none;
        // opacity: 0;
        top: calc(100% + 2px);
        left: -2px;
        right: -2px;
        transition: opacity .3s ease, transform .6s ease;
        // transform: translate3d(0, 30px, 0);
        z-index: 3;
        transition: all .3s ease;
        overflow: hidden;
    }

    &__list-inner {
        padding: 28px 20px;
        background: $skinny-light;
        font-weight: 500;
        transform: translate3d(0, -100%, 0);
        transition: transform .5s cubic-bezier(0.86, 0.13, 0.12, 0.86);
        border: 2px solid $gold;
        border-top: none;
    }

    &__item {
        cursor: pointer;
        margin-bottom: 21px;
        display: flex;
        align-items: center;
        text-decoration: none;
        position: relative;
        color: $dark;
        @extend %text-size;

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    &__checkbox {
        width: 20px;
        height: 20px;
        margin-right: 15px;
        border-radius: 50%;
        background: #F8F5EF;

        input[type="checkbox"] {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            z-index: 1;
            cursor: pointer;

            &:checked {
                & + .select__checkbox-circle {
                    background: $gold;

                    > svg {
                        transform: translate(-50%, -50%) scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }

    &__checkbox-circle {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 100%;
        position: relative;
        transition: all .3s ease;

        > svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) scale(0.1);
            opacity: 0;
            transition: opacity .3s ease, transform .4s ease;
        }
    }

    &__input {
        width: 100%;
        padding: 10px 40px 11px 20px;
        border: none;
        background: none;
        color: $dark;
        @extend %text-size;
        line-height: 19px;
        outline: none;
        cursor: pointer;
    }
}

@media screen and (max-width: $mobile) {
    .select {
        width: 100%;
    }
}
