.ie-notify {
    position: fixed;
    left: 50%; bottom: 0;
    transform: translate(-50%, 100%);
    width: rem(780);
    z-index: 999;

    &__inner {
        padding: rem(30) rem(30) rem(40);
        background: #ffbdb1;
        border-radius: rem(4) rem(4) 0 0;
        box-shadow: 0 rem(19) rem(38) rgba(0, 0, 0, .30), 0 rem(15) rem(12) rgba(0, 0, 0, .22);
        text-align: center;
    }

    &__title {
        font-size: rem(18);
        font-weight: 700;
        margin-bottom: rem(10);
    }

    &__subtitle { margin-bottom: rem(20); }

    &--animate { animation: show .8s ease-in-out forwards; }
}

@keyframes show {
    from { transform: translate(-50%, 100%); }

    to { transform: translate(-50%, 0); }
}
