.accordion {
    background: #F8F5EF;
    margin-bottom: 20px;

    &__inner {
        position: relative;
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px 30px 25px;
        cursor: pointer;
        user-select: none;
    }

    &__title {
        font-family: $font-secondary;
        font-size: 18px;
        line-height: 22px;
        color: $dark;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        padding-right: 50px;
        // max-width: calc(100% - 6.25rem);
    }

    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .3s ease;
        cursor: pointer;
        transform: rotate(180deg);

        > svg {
            path {
                transition: stroke .3s ease;
            }
        }
    }

    &__main {
        overflow: hidden;
        max-height: 0;
        padding: 0 30px;
        transition: max-height .3s ease;
    }

    &_active {
        .accordion {
            &__icon {
                transform: none;
            }
        }
    }
}

@media screen and (max-width: $laptop) {
    .accordion {
        &__header {
            padding: 20px;
        }

        &__main {
            padding: 0 20px;
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .accordion {

    }
}

@media screen and (max-width: $mobile) {
    .accordion {

        &__title {
            font-size: 14px;
            line-height: 17px;
            padding-right: 20px;
        }
    }
}
