.slide-scroll {
    height: 100%;

    &__content {
        position: relative;
    }

    &__media {
        position: relative;
        height: 100%;
        transition: transform 1s ease;
    }

    &__video {
        height: 100%;

        video {
            @extend %video-cover;
        }
    }

    &__img {
        height: 100%;
        background-size: cover;
        background-position: center center;
    }

    &__media-desc {
        position: absolute;
        top: 0;
        right: 50%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &__media-icon {
        position: absolute;
        bottom: rem(54);
        left: 0;
        right: 0;
        display: flex;
        justify-content: center;
        animation: swing 2s ease infinite;
    }

    &__media-img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
        transition: opacity .3s ease;
    }

    &__content {
        height: 100%;
        background: $skinny;
        transition: transform 1s ease;
    }

    &_scrolled {
        .slide-scroll {
            &__content {
                transform: translate3d(0, -100%, 0);
            }

            &__media {
                transform: translate3d(0, -50%, 0);
            }
        }
    }

    .no-spot {
        canvas {
            display: none;
        }
    }
}

@media screen and (max-width: $laptop) {
    .slide-scroll {
        &__media-icon {
            bottom: rem(44);

            svg {
                width: 32px;
                height: 32px;
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .slide-scroll {
        height: auto;

        &__media {
            height: rem(480);
        }

        &__media-desc {
            right: 0;
            width: 100%;
            justify-content: center;
        }

        &__media-icon {
            display: none;
        }

        &_scrolled {
            .slide-scroll {
                &__content {
                    transform: none;
                }

                &__media {
                    transform: none;
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .slide-scroll {
        &__media-desc {
            // height: 100%;
        }
    }
}
