.radio {
    margin-top: rem(26);

    &__control {
        width: 1px;
        height: 1px;
        opacity: 0;

        &:checked + .radio {
            &__circle {
                border: solid #fff;
                background: transparent;
                border-width: rem(6);
            }
        }
    }

    &__label {
        display: flex;
        align-items: baseline;
        position: relative;
        cursor: pointer;
    }

    &__circle {
        background: #F8F5EF;
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        display: block;
        border-radius: 50%;
        position: relative;
        top: rem(4);
        border: 0.33333rem solid transparent;
        transition: all .3s ease;
    }

    &__text {
        padding-left: rem(14);
        user-select: none;
        color: $gray;
    }

    &__link {
        color: $dark;
        position: relative;
        transition: opacity .3s ease;
    }
}

@media screen and (max-width: $mobile) {
    .radio {
        &__text {
            font-size: 12px;
            line-height: 22px;
        }
    }
}
