.summary {
    margin: rem(122) 0 0 0;
    display: flex;
    justify-content: center;

    &__container {
        max-width: rem(941);
        text-align: center;
    }

    p {
        font-family: $font-secondary;
        font-size: rem(36);
        line-height: rem(43);
        color: $dark;
        margin: rem(40) 0;
    }
}

@media screen and (max-width: $laptop) {
    .summary {
        &:last-child {
            margin-bottom: 0;
        }

        p {
            font-size: rem(28);
            line-height: rem(34);
            margin: rem(30) 0;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .summary {
        margin: rem(59) 0;
        padding: 0 50px;

        &:last-child {
            margin-bottom: 0;
        }

        p {
            font-size: 28px;
            line-height: 34px;
            margin: 30px 0;

            &:first-child {
                margin-top: 0;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .summary {
        margin: rem(35) 0;
        padding: 0 10px;

        p {
            font-size: 18px;
            line-height: 22px;
            margin: 20px 0;

            &:first-child {
                margin-top: 0;
            }
        }
    }
}
