.page {
    height: 100%;
    overflow: hidden;

    &__content {
        height: inherit;
    }

    &_main {
        background: $main-theme;
    }

    &_catalog {
        padding-top: rem(160);
        background: $skinny;
    
        &::before {
            @extend %pseudo;
            height: rem(142);
            background: $skinny;
            left: 0;
            top: 0;
            width: 100%;
            z-index: 2;
        }
    }

    &_people, &_where-to-buy {
        background: $skinny;
    }
}

@media screen and (max-width: $laptop) {
    .page {
        &_catalog {
            padding-top: rem(105);

            &::before {
                height: rem(92);
            }
        }
    }
}

@media screen and (max-width: $tablet-landscape) {
    .page {
        &_catalog {
            padding-top: rem(140);
    
            &::before {
                height: rem(120);
            }
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .page {
        overflow: visible;
        height: auto;
        min-height: var(--app-height);

        &_catalog {
            padding-top: rem(115);

            &::before {
                display: none;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .page {

        &_catalog {
            padding-top: 74px;
        }

        &_product {
            .slider-scroll__slide {
                padding-bottom: 0;
            }
        }
    }
}
