.media-col {
    display: flex;
    justify-content: center;
    margin: rem(130) 0 rem(100);

    &__container {
        max-width: 800px;
    }

    &__title {
        @extend .block-media__title;
    }

    &__body {
        position: relative;
        @extend .text;

        .media-col {
            &__link-overlay {
                padding: 0;
                margin: 0;
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;

                &::before {
                    display: none;
                }
            }
        }
    }

    &__link {
        position: absolute;
        left: calc(100% + #{rem(250)});
        top: 0;
    }

    &__link-wrapper {
        display: inline-block;
        position: relative;
        padding-left: 86px;
    }

    &__link-icon {
        background: $skinny;
        position: absolute;
        top: 50%;
        left: 0;
        width: 50px;
        height: 50px;
        border: 1px solid $gold;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 5px;
        transform: translate3d(0, -50%, 0);
    }

    &__link-text {
        font-size: 16px;
        line-height: 36px;
        color: $dark;
        white-space: nowrap;
    }

    &_type {
        &_left {
            display: block;
            justify-content: left;

            .media-col {
                &__container {
                    @extend %col-offset;
                }
            }
        }
    }

    .title-offset + & {
        margin-top: rem(60);
    }
}

@media screen and (max-width: $laptop) {
    .media-col {
        margin: rem(100) 0;

        &__container {
            max-width: 640px;
        }

        &__link {
            left: calc(100% + #{rem(200)});
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .media-col {
        margin: 65px 0;

        &__link {
            margin-top: 45px;
            position: relative;
            left: 0;
        }
    }
}

@media screen and (max-width: $mobile) {
    .media-col {
        margin: 40px 0;

        &__link {
            margin-top: 25px;
        }

        &__link-wrapper {
            padding-left: 56px;
        }

        &__link-icon {
            width: 36px;
            height: 36px;

            svg {
                width: 16px;
                height: 8px;
            }
        }

        .title-offset + & {
            margin-top: 25px;
        }
    }
}
