.info {
    font-family: $font-secondary;
    font-size: rem(36);
    line-height: rem(43);
    color: $dark;
    margin: rem(75) 0 rem(48);
}

@media screen and (max-width: $laptop) {
    .info {
        margin: rem(46) 0;
        font-size: rem(32);
        line-height: rem(38);
    }
}

@media screen and (max-width: $tablet-portrait) {
    .info {
        font-size: 28px;
        line-height: 34px;
        margin: 43px 0 31px;
    }
}

@media screen and (max-width: $mobile) {
    .info {
        font-size: 18px;
        line-height: 22px;
        margin: 30px 0 35px;
    }
}
