.events-layout {
    display: flex;
    height: 100%;
    background: $skinny;

    &__img {
        min-width: rem(790);
        background-size: cover;
        background-position: center center;
        position: relative;

        .events-layout {
            &__back {
                display: none;
                @extend %absolute-right;
            }
        }
    }

    &__back {
        position: absolute;
        top: rem(158);
        right: rem(80);
    }

    &__content-wrp {
        position: absolute;
        top: rem(244);
        right: 0;
        width: rem(998);
        @extend %padding-right;
    }

    &__content-scroll {
        width: rem(685);
        height: rem(650);
    }
}

@media screen and (max-width: $laptop) {
    .events-layout {
        &__content-wrp {
            top: rem(206);
            width: rem(768);
        }

        &__back {
            top: rem(127);
            right: rem(61);
        }

        &__img {
            min-width: rem(601);
        }

        &__content-scroll {
            width: rem(625);
            height: 65vh;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .events-layout {
        flex-wrap: wrap;
        padding-top: 107px;

        &__back {
            display: none;
        }

        &__img {
            min-width: 100%;
            height: rem(481);

            .events-layout {
                &__back {
                    display: block;
                    top: 34px;
                }
            }

            .back__text {
                color: #fff;
            }

            .back__icon {
                path {
                    stroke: #fff;
                }
            }
        }

        &__content {
            width: 100%;
            padding: 60px 60px 0;
        }

        &__content-wrp {
            position: relative;
            top: 0;
            width: 100%;
            padding-right: 0;
        }

        &__content-scroll {
            width: 100%;
            height: auto;
        }
    }
}

@media screen and (max-width: $mobile) {
    .events-layout {
        padding-top: 74px;

        &__img {
            height: rem(380);

            .events-layout {
                &__back {
                    display: none;
                }
            }
        }

        &__content {
            padding: 40px 20px 0;
        }

        .text {
            h1 {
                font-size: rem(26);
                line-height: rem(31);
                margin: rem(10) 0 rem(25);
            }
        }
    }
}
