.back {
    padding-left: rem(24);
    position: relative;

    &__icon {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate3d(0, -50%, 0);
    }

    &__text {
        line-height: rem(16);
        color: #363636;
    }
}

@media screen and (max-width: $tablet-portrait) {
    .back {
        &__text {
            line-height: 16px;
        }
    }
}
