.error-page {
    &__inner {
        min-height: 100vh;
        min-height: var(--app-height);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__icon {
        margin-bottom: rem(100);
        width: 112px;
        height: 140px;

        svg {
            fill: $dark;
        }
    }

    &__status,
    &__message {
        margin-bottom: rem(50);
        color: $dark;
    }

    &__status {
        font-weight: 700;
        font-size: rem(78);
        color: $main-theme;
    }

    &__message {
        max-width: rem(700);
        text-align: center;
        text-transform: uppercase;
        line-height: 1.4;
    }
    
    .back {
        color: $dark;
    }
}

@media screen and (max-width: $tablet-landscape) {
    .error-page {
        height: 100vh;
        height: var(--app-height);
        overflow: auto;
        padding: 80px 0;

        &__inner {
            min-height: 100%;
        }

        &__message {
            max-width: rem(502);
        }
    }
}

@media screen and (max-width: $mobile) {
    .error-page {
        &__icon {
            margin-bottom: 40px;
            width: 53px;
            height: 65px;

            img {
                width: 100px;
            }
        }

        &__status {
            font-weight: 700;
            font-size: 48px;
            line-height: 58px;
            margin-bottom: 20px;
        }

        &__message {
            font-size: 14px;
            line-height: 17px;
            margin-bottom: 41px;
            max-width: 435px;
        }
    }
}
