.contacts-layout {
    display: flex;
    height: 100%;
    background: $skinny;

    &__map {
        min-width: rem(790);
    }

    &__content {
        width: 100%;
    }

    &__content-wrp {
        position: absolute;
        top: rem(205);
        right: 0;
        width: rem(998);
        @extend %padding-right;
    }

    &__content-scroll {
        width: rem(682);
        height: rem(650);
    }

    &__label {
        font-family: $font-secondary;
        font-size: rem(36);
        line-height: rem(43);
        margin: 60px 0 37px;
        color: $dark;

        & + .accordion-grid {
            margin-top: 0;
        }
    }
}

@media screen and (max-width: $laptop) {
    .contacts-layout {
        &__map {
            min-width: rem(601);
        }

        &__content-wrp {
            top: rem(146);
            width: rem(768);
        }

        &__content-scroll {
            width: rem(608);
            height: 70vh;
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .contacts-layout {
        flex-wrap: wrap;

        &__map {
            width: 100%;
            height: rem(481);
        }

        &__content {
            padding: 55px 60px 0;
        }

        &__content-wrp {
            top: 0;
            position: relative;
            padding-right: 0;
            width: 100%;
        }

        &__content-scroll {
            width: 100%;
            height: auto;
        }
    }
}

@media screen and (max-width: $mobile) {
    .contacts-layout {
        &__map {
            margin-top: 20px;
            height: 50vh;
            width: 100%;
            min-width: 100%;
            order: 2;
        }

        &__content {
            padding: 90px 20px 0;
            order: 1;
        }

        &__label {
            font-size: rem(24);
            line-height: rem(26);
            margin: 0 0 30px;
        }

        .slider-scroll__slide {
            padding-bottom: 0;
        }
    }
}
