.card-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 50px;

    &__body {
        position: relative;
        width: 100%;
        height: 100%;
    }

    &__label {
        font-family: $font-secondary;
        font-size: 18px;
        line-height: 22px;
    }

    &__text {
        max-width: 618px;
        position: absolute;
        left: 0;
        bottom: 0;
    }

    &__title {
        font-family: $font-secondary;
        font-size: 30px;
        line-height: 36px;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__desc {
        font-size: 16px;
        line-height: 28px;
        margin: 29px 0 31px;
    }

    &__more {
        position: relative;
    }

    &__more-text {
        font-size: 16px;
        line-height: 19px;
        display: inline-block;
        padding-right: 37px;
        position: relative;

        &::before {
            @extend %pseudo;
            background: url("data:image/svg+xml,%3Csvg viewBox='0 0 24 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 8.13109H21L13.7586 0.933594' stroke='white' stroke-width='2'/%3E%3C/svg%3E%0A") no-repeat;
            top: 50%;
            right: 0;
            transform: translate3d(0, -50%, 0);
            width: 21px;
            height: 7px;
            transition: transform .3s ease;
        }
    }
}

@media screen and (max-width: $laptop) {
    .card-content {
        padding: 30px;

        &__label {
            font-size: 16px;
            line-height: 19px;
        }

        &__text {
            max-width: 550px;
        }

        &__title {
            font-size: 28px;
            line-height: 34px;
            margin-bottom: 15px;
        }

        &__desc {
            margin: 20px 0;
            line-height: 19px;
        }

        &__more-text {
            padding-right: 33px;
        }
    }
}

@media screen and (max-width: $mobile) {
    .card-content {
        &__label {
            font-size: 14px;
            line-height: 17px;
        }

        &__title {
            font-size: 18px;
            line-height: 22px;
        }

        &__desc {
            font-size: 14px;
            line-height: 20px;
            margin: 15px 0;
        }

        &__more-text {
            font-size: 14px;
            line-height: 16px;

            &::before {
                transform: translate3d(0, -65%, 0);
            }
        }
    }
}
