.news-grid {
    margin-bottom: rem(50);

    &__container {
        display: flex;
        margin: 0 rem(-30);
        flex-wrap: wrap;
    }

    &__item {
        padding: 0 rem(30);
        width: (100% / 3);
        position: relative;
        margin-bottom: rem(60);

        &:nth-child(3n+2) {
            top: rem(120);
        }

        &:nth-child(3n+3) {
            top: rem(60);
        }
    }
}

@media screen and (max-width: $laptop) {
    .news-grid {
        &__container {
            margin: 0 rem(-21);
        }

        &__item {
            padding: 0 rem(21);
        }
    }
}

@media screen and (max-width: $tablet-portrait) {
    .news-grid {
        margin-bottom: 0;

        &__container {
            margin: 0 rem(-20);
        }

        &__item {
            width: (100% / 2);
            padding: 0 rem(20);
            margin-bottom: 50px;

            &:nth-child(3n+2) {
                top: 0;
            }

            &:nth-child(3n+3) {
                top: 0;
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .news-grid {
        &__container {
            margin: 0;
        }

        &__item {
            padding: 0;
            width: 100%;
            margin-bottom: 30px;
        }
    }
}
