.link {
    @extend %text-size;
    @extend %btn-normalize;
    position: relative;
    padding-right: rem(35);
    color: $dark;
    margin: rem(50) 0;
    display: inline-block;
    cursor: pointer;

    &_clamp {
        margin: rem(25) 0;
        background: $green;
        color: #fff;
        padding-right: 0;
        padding: rem(20) rem(50);
        border-radius: 5px;
        transition: all .4s ease;

        &:hover {
            transition: all .4s ease;
            background:#fff;
            color: $green;
        }

        &::before {
            display: none;
        }
    }

    &_muted {
        color: $gray;
        padding-right: 0;

        &:before {
            display: none;
        }
    }

    &::before {
        @extend %pseudo;
        // background: url("data:image/svg+xml,%3Csvg width='25' height='10' viewBox='0 0 25 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.845551 8.41406H21.8456L14.6042 1.41406' stroke='%233CB7D1' stroke-width='2'/%3E%3C/svg%3E");
        background: url("data:image/svg+xml,%3Csvg width='21' height='7' viewBox='0 0 24 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 8H21L13.7586 1' stroke='%233CB7D1' stroke-width='2'/%3E%3C/svg%3E%0A") no-repeat;
        // background: url("data:image/svg+xml,%3Csvg width='21' height='7' viewBox='0 0 24 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 8H21L13.7586 1' stroke='%2323AEA3' stroke-width='2'/%3E%3C/svg%3E%0A") no-repeat;
        top: 30%;
        right: 0;
        transform: translate3d(0, -50%, 0);
        width: rem(21);
        height: rem(7);
        transition: transform .3s ease;
    }

    &:hover {
        &::before {
            transform: translate3d(20%, -50%, 0);
        }
    }
}

@media screen and (max-width: $laptop) {
    .link {
        margin: rem(43) 0;
        padding-right: 35px;

        &::before {
            width: 21px;
            height: 7px;
            top: 40%;
        }

        &_clamp {
            margin: 46px 0 0;
            padding-right: 0;
            padding: rem(20) rem(50);
        }
    }
}

@media screen and (max-width: $mobile) {
    .link {
        margin: rem(30) 0;

        &_clamp {
            padding: rem(10) rem(25);
        }
    }
}
