.form-group {
    margin-bottom: 20px;
    display: block;
    position: relative;
    z-index: 1;

    &_active {
        z-index: 10;
    }

    & + &_submit {
        margin-top: 27px;
    }

    &__input-file {
        width: 100%;
        z-index: 20;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        // visibility: hidden;
    }

    &__text {
        font-size: 14px;
        line-height: 16px;
        margin-left: 15px;
        color: #363636;
        cursor: pointer;

    }

    &__file {
         cursor: pointer;
        display: flex;
        align-items: center;
    }

    &__file-delete {
        margin-left: 15px;
        z-index: 80;
    }
}

@media screen and (max-width: $mobile) {
    .form-group {
        .link[type="submit"] {
            margin: 10px 0 0;
        }
    }
}
