.img-magnifier-glass {
    position: absolute;
    border: 1px solid #000;
    cursor: none;
    width: 450px;
    height: 300px;
    z-index: 10000;

    @media screen and (max-width: $laptop) {
        // display: none;
        width: 380px;
        height: 220px;
    }


    @media screen and (max-width: 1366px) {
        display: none;
    }
}
